import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators, FormControl } from "@angular/forms";

import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "../../../core/consultaService/auth.service";
import { ReportesService } from "../../../core/consultaService/reportes.service";
import { ExcelRPSService } from "../../../core/excel/excelRPS.service";
import { UtilService } from "../../../services/util.service";

// declare var $: any;
import * as _ from "lodash";
const swal = require("sweetalert");

@Component({
  selector: "app-piramidal-simple",
  templateUrl: "./piramidalSimple.component.html"
})
export class PiramidalSimpleComponent implements OnInit {
  piramidalSimpleReportes: any = {};
  request: any = {};
  clientes: any = {};
  indexPeriodos: any = {};
  forma: FormGroup;
  fechaActual = new Date();
  user: any = {};

  meses: Array<any> = [
    { codMes: "01", descMes: "Enero" },
    { codMes: "02", descMes: "Febrero" },
    { codMes: "03", descMes: "Marzo" },
    { codMes: "04", descMes: "Abril" },
    { codMes: "05", descMes: "Mayo" },
    { codMes: "06", descMes: "Junio" },
    { codMes: "07", descMes: "Julio" },
    { codMes: "08", descMes: "Agosto" },
    { codMes: "09", descMes: "Septiembre" },
    { codMes: "10", descMes: "Octubre" },
    { codMes: "11", descMes: "Noviembre" },
    { codMes: "12", descMes: "Diciembre" }
  ];

  anios: Array<any> = [];
  desdeAnio: string;
  desdeMes: string;

  hastaAnio: string;
  hastaMes: string;
  hastaDate: Date;
  desdeDate: Date;
  periodos: number;

  constructor(
    private authService: AuthService,
    private reportesService: ReportesService,
    private excelService: ExcelRPSService,
    private utilService: UtilService,
    public cargador: NgxSpinnerService
  ) {
    this.forma = new FormGroup({
      desdeAnio: new FormControl("", Validators.required),
      desdeMes: new FormControl("", Validators.required),
      hastaAnio: new FormControl("", Validators.required),
      hastaMes: new FormControl("", Validators.required)
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.cargaComboAnios();

    this.forma.controls.desdeMes.setValue(
      this.dateToStringMonthFormat(this.fechaActual)
    );
    this.forma.controls.hastaMes.setValue(
      this.dateToStringMonthFormat(this.fechaActual)
    );
    this.forma.controls.desdeAnio.setValue(this.fechaActual.getFullYear());
    this.forma.controls.hastaAnio.setValue(this.fechaActual.getFullYear());
  }

  cargaComboAnios() {
    for (let i = 1900; i <= 2100; i++) {
      let anio = {
        codAnio: i,
        descAnio: `${i}`
      };
      this.anios.push(anio);
    }
  }

  generar() {
    this.cargador.show();
    this.desdeAnio = this.forma.controls.desdeAnio.value;
    this.desdeMes = this.forma.controls.desdeMes.value;
    let desde = this.desdeAnio + this.desdeMes;
    this.desdeDate = new Date(
      Number(this.desdeAnio),
      Number(this.desdeMes) - 1
    );

    this.hastaAnio = this.forma.controls.hastaAnio.value;
    this.hastaMes = this.forma.controls.hastaMes.value;
    let hasta = this.hastaAnio + this.hastaMes;
    this.hastaDate = new Date(
      Number(this.hastaAnio),
      Number(this.hastaMes) - 1
    );

    if (this.validarEntradasFormulario()) {
      this.request.nRutOperador = Number(this.user.nrutoperador);
      this.request.sPeriodoDesde = desde;
      this.request.sPeriodoHasta = hasta;
      this.piramidalSimpleReporte();
    }
  }

  validarEntradasFormulario(): boolean {
    let _validarEntradasFormulario = true;
    if (this.hastaDate < this.desdeDate) {
      swal("Atención", "La fecha 'Hasta' debe ser mayor a la fecha 'Desde'!", "error");
      _validarEntradasFormulario = false;
      this.cargador.hide();

    }

    this.periodos = this.diff_months(this.desdeDate, this.hastaDate);

    if (this.periodos >= 24) {
      swal("Atención", "Período a Consultar no debe ser mayor a 2 años!", "error");
      _validarEntradasFormulario = false;
      this.cargador.hide();

    }

    return _validarEntradasFormulario;
  }

  piramidalSimpleReporte() {
    this.reportesService.getRptPiramidalSimple(this.request).subscribe(
      (data: any) => {
        this.cargador.hide();
        if (data) {
          if (data.length == 0 && data.error.codError == 0) {
            swal("Atención", "No se encontraron resultados", "info");
          } else {
            this.generateExcel(data.piramidal);
          }
        }
        if (data.error.codError != 0) {
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        }
      },
      error => {
        this.cargador.hide();
        swal("Atención", "Ha ocurrido un error inesperado!", "error");
      }
    );
  }

  generateExcel(data: Array<any>) {
    // ! Generacion de la cabezera excel, contadores e indices
    let headerExcel = ["Rut", "Nombre"];

    let indexPeriodo = 0;

    for (let i = Number(this.desdeAnio); i <= Number(this.hastaAnio); i++) {
      if (Number(this.desdeAnio) === Number(this.hastaAnio)) {
        for (let j = Number(this.desdeMes); j <= Number(this.hastaMes); j++) {
          let periodoKilos = "KG   " + j + "-" + i;
          let periodoPnv = "$   " + j + "-" + i;

          headerExcel.push(periodoKilos);
          headerExcel.push(periodoPnv);

          var ind = j + "-" + i;
          indexPeriodo += 2;
          this.indexPeriodos[ind] = indexPeriodo;
        }
      } else if (i === Number(this.desdeAnio)) {
        for (let j = Number(this.desdeMes); j <= 12; j++) {
          let periodoKilos = "KG   " + j + "-" + i;
          let periodoPnv = "$   " + j + "-" + i;

          headerExcel.push(periodoKilos);
          headerExcel.push(periodoPnv);

          var ind = j + "-" + i;
          indexPeriodo += 2;
          this.indexPeriodos[ind] = indexPeriodo;
        }
      } else if (i === Number(this.hastaAnio)) {
        for (let j = 1; j <= Number(this.hastaMes); j++) {
          let periodoKilos = "KG   " + j + "-" + i;
          let periodoPnv = "$   " + j + "-" + i;

          headerExcel.push(periodoKilos);
          headerExcel.push(periodoPnv);

          var ind = j + "-" + i;
          indexPeriodo += 2;
          this.indexPeriodos[ind] = indexPeriodo;
        }
      } else {
        for (let j = 1; j <= 12; j++) {
          let periodoKilos = "KG   " + j + "-" + i;
          let periodoPnv = "$   " + j + "-" + i;

          headerExcel.push(periodoKilos);
          headerExcel.push(periodoPnv);

          var ind = j + "-" + i;
          indexPeriodo += 2;
          this.indexPeriodos[ind] = indexPeriodo;
        }
      }
    }

    // ! Generacion del contenedor intermedio de data

    this.clientes = {};

    for (let i in data) {
      var key = this.rutFormater(
        data[i].cli_nrutcliente,
        data[i].cli_sdigverificador
      );

      if (!this.clientes.hasOwnProperty(key)) {
        let cliente = {
          Rut: key,
          Nombre: data[i].nombre
        };
        this.clientes[key] = cliente;
      }

      let kgPvn = {
        kilos: data[i].kilos,
        pnv: data[i].pnv
      };
      var dt = this.sPeriodo2ExcelPeriodo(data[i].rpp_speriodo);
      this.clientes[key][dt] = kgPvn;
    }

    let dataExcel = [];
    var dataTotales = Array.from(Array(Number(this.periodos) * 2 + 4), () => 0);



    for (let i in this.clientes) {
      var cliente = Array.from(Array(Number(this.periodos) * 2 + 4), () => 0);
      cliente[0] = this.clientes[i].Rut;
      cliente[1] = this.clientes[i].Nombre;

      for (let j in this.clientes[i]) {
        if (this.indexPeriodos.hasOwnProperty(j)) {
          cliente[this.indexPeriodos[j]] = this.clientes[i][j]["kilos"];
          cliente[this.indexPeriodos[j] + 1] = this.clientes[i][j]["pnv"];

          dataTotales[this.indexPeriodos[j]] = dataTotales[this.indexPeriodos[j]] + this.clientes[i][j]["kilos"];
          dataTotales[this.indexPeriodos[j] + 1] = dataTotales[this.indexPeriodos[j] + 1] + this.clientes[i][j]["pnv"];

        }
      }
      dataExcel.push(cliente);
    }

    let fechaExcel = this.utilService.dateToLocalStringFormat(this.fechaActual);
    let titulo = `Reporte Piramidal Simple ${this.dateToStringDateTitleExcelFormat(new Date())}`;
    let fileName = `Reporte Piramidal Simple ${this.utilService.dateToStringDateFullFormat(new Date())}00`;
    let tamaño = Number(this.periodos * 2 + 2);

    this.excelService.generateExcel(
      titulo,
      headerExcel,
      dataExcel,
      dataTotales,
      fechaExcel,
      fileName,
      tamaño
    );
    this.cargador.hide();

    swal("Perfecto", "Su reporte ha sido generado con exito!", "success");
  }


  // Funciones Auxiliares

  rutFormater(rut: string, digverificador: string) {
    return rut + "-" + digverificador;
  }

  diff_months(dateFrom: any, dateTo: any) {
    return (
      dateTo.getMonth() -
      dateFrom.getMonth() +
      12 * (dateTo.getFullYear() - dateFrom.getFullYear())
    );
  }

  sPeriodo2ExcelPeriodo(rpp_speriodo) {
    //   rpp_speriodo
    //   201905 => 5-2019
    let anio = rpp_speriodo.substr(0, 4);
    let mes = String(Number(rpp_speriodo.substr(4, 2)));

    return `${mes}-${anio}`;
  }

  // Thu Jul 18 2019 12:08:08 GMT-0400 (hora estándar de Chile) => 201907
  dateToStringDateTitleExcelFormat(date) {
    let anio = date.getFullYear();
    let mes = date.getMonth() + 1;

    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${anio}${mesStr}`;
    return fechaString;
  }

  // Thu Jul 18 2019 12:08:08 GMT-0400 (hora estándar de Chile) => 07
  dateToStringMonthFormat(date) {
    let mes = date.getMonth() + 1;
    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${mesStr}`;
    return fechaString;
  }
}
