import { Injectable } from "@angular/core";
import { Workbook } from "exceljs/dist/exceljs.min";
import * as fs from "file-saver";
import * as logoFile from "./logo.js";

@Injectable({
  providedIn: "root"
})
export class ExcelRACService {
  constructor() {}
  generateExcel(
    title: string,
    headerExcel: any,
    dataExcel: any,
    fechaExcel: any,
    fileName: string
  ) {
    //Titulo, Encabezado y Datos

    //Creacion del libro y las hojas
    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet(title);

    //Agrega filas y su formato

    worksheet.addRow([]);

    //Add Image
    let logo = workbook.addImage({
      base64: logoFile.logoBase64,
      extension: "png"
    });
    worksheet.addImage(logo, "A1:A3");

    //Fila de separacion (en blanco)
    worksheet.addRow([]);

    //Fecha
    worksheet.getCell("A5").value = "Fecha";
    worksheet.getCell("B5").value = fechaExcel;

    //Fila de separacion (en blanco)
    worksheet.addRow([]);
    worksheet.addRow(["Reporte Acciones Comerciales"]);
    worksheet.mergeCells("A7:K7");
    worksheet.getCell("A7").alignment = {
      vertical: "middle",
      horizontal: "center"
    };
    worksheet.getCell("A7").fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" },
      bgColor: { argb: "" }
    };

    //Add Header Row
    let headerRow = worksheet.addRow(headerExcel);

    // Estilo de celda : Fill and Border
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFB0C4DE" },
        bgColor: { argb: "" }
      };
    });

    worksheet.addRows(dataExcel);

    worksheet.getColumn(1).width = 15;
    worksheet.getColumn(2).width = 45;
    worksheet.getColumn(3).width = 29;
    worksheet.getColumn(4).width = 13;
    worksheet.getColumn(5).width = 14;
    worksheet.getColumn(6).width = 12;
    worksheet.getColumn(7).width = 13;
    worksheet.getColumn(8).width = 13;
    worksheet.getColumn(9).width = 45;
    worksheet.getColumn(10).width = 21;
    worksheet.getColumn(11).width = 12;

    //Pie de pagina
    let footerRow = worksheet.addRow(["2019-Disvet"]);
    footerRow.getCell(1).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };

    //Merge Cells
    worksheet.mergeCells(`A${footerRow.number}:D${footerRow.number}`);

    worksheet.eachRow(function(row, rowNumber) {
      row.eachCell(function(cell, colNumber) {
        cell.border = {
          top: { style: "thin" },
          left: { style: "thin" },
          bottom: { style: "thin" },
          right: { style: "thin" }
  
        };
      });
    });

    //Generate Excel File with given name

    if (fileName) {
      var exportName = `${fileName}.xlsx`;
    } else {
      var exportName = `${title}.xlsx`;
    }

    workbook.xlsx.writeBuffer().then((data: any) => {
      let blob = new Blob([data], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      });
      fs.saveAs(blob, exportName);
    });
  }
}
