import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';

import { ReportesService } from '../../../core/consultaService/reportes.service';
import { UtilService } from './../../../services/util.service';
import { ExcelRACService } from '../../../core/excel/excelRAC.service';
import { NgxSpinnerService } from "ngx-spinner";

const swal = require('sweetalert');

@Component({
	selector: 'app-acciones-comerciales',
	templateUrl: './accionesComerciales.component.html'
})

export class AccionComercialesComponent implements OnInit {

	fechaActual = new Date();
	request: any = {};
	operadores: Array<any> = [];
  
	forma: FormGroup;
	user: any = {};
	subData: any;



	meses: Array<any> = [
	  { codMes: "01", descMes: "Enero" },
	  { codMes: "02", descMes: "Febrero" },
	  { codMes: "03", descMes: "Marzo" },
	  { codMes: "04", descMes: "Abril" },
	  { codMes: "05", descMes: "Mayo" },
	  { codMes: "06", descMes: "Junio" },
	  { codMes: "07", descMes: "Julio" },
	  { codMes: "08", descMes: "Agosto" },
	  { codMes: "09", descMes: "Septiembre" },
	  { codMes: "10", descMes: "Octubre" },
	  { codMes: "11", descMes: "Noviembre" },
	  { codMes: "12", descMes: "Diciembre" }
	];

	anios: Array<any> = [];
	Anio: any;
	Mes: any;

        
	constructor(
		private excelService: ExcelRACService,
		public cargador: NgxSpinnerService,
		private utilService: UtilService,
		private reportesService: ReportesService) {

			this.forma = new FormGroup({
				mes: new FormControl("", Validators.required),
				anio: new FormControl("", Validators.required)
			  });

	}

	ngOnInit() {
		  this.setGenerar();
	}


  setGenerar() {
    this.cargaComboAnios();
    this.forma.controls.mes.setValue(this.dateToStringMonthFormat(this.fechaActual));
    this.forma.controls.anio.setValue(this.fechaActual.getFullYear());
  }


	cargaComboAnios() {
		for (let i = 1900; i <= 2100; i++) {
		  let anio = {
			codAnio: i,
			descAnio: `${i}`
		  };
		  this.anios.push(anio);
		}
	  }


	  generar() {
		this.cargador.show();
		this.Anio = this.forma.controls.anio.value;
		this.Mes = this.forma.controls.mes.value;
		let fecha = this.Anio + this.Mes;	
	
		this.request.nrutoperador = 0;
		this.request.sfecha = fecha;
	
		this.accionesComerciales();
	  }

	  accionesComerciales() {
		this.reportesService.getRptAccionesComerciales(this.request)
		.subscribe((data: any) => {
		  this.cargador.hide();
		  if (data.accionesComerciales.length === 0 && data.error.codError === 0 ) {
			swal('Atención', 'No se encontraron resultados', 'info');
		  } else {
			  if (data.accionesComerciales === null) {
				  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
			  } else {
				this.generateExcel(data.accionesComerciales);
			  }
		  }
		  if (data.error.codError !== 0) {
			  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
		  }
	  },
	  error => {
		  this.cargador.hide();
		  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
	  });
	
	  }

	  generateExcel(data: Array<any>) {
		const headerExcel = [
		  "Id Accion Com.",
		  "Accion Comercial",
		  "Tipo Descuento",
		  "Fecha Inicio",
		  "Fecha Termino",
		  "Cod Formato",
		  "Cod Variedad",
		  "Rut Operador",
		  "Operador",
		  "Categoria de Negocio",
		  "% Descuento"
		];
		const dataExcel = [];
	
		data.forEach(e => {
			let rut: string = "";
			if (e.eac_nrutoperador !== null && e.eac_nrutoperador !== 0) {
				rut = this.rutFormater(String(this.utilService.formatDotNumber(e.eac_nrutoperador)), e.ope_sdigverificador);
			}


		  dataExcel.push([
			e.aco_nidaccion,
			e.aco_sdescripcion,
			e.par_deslargo01,
			this.utilService.dateStringServiceToReverseStringFormat(e.aco_dfhoinicio),
			this.utilService.dateStringServiceToReverseStringFormat(e.aco_dfhotermino),
			e.dac_scodformato,
			e.dac_scodvariedad,
			rut,
			e.ope_snombre,
			e.can_sdescatnegocio,
			e.dac_nporcdescto
		  ]);
		});
	
		let fechaExcel = this.utilService.dateToStringDateBarFormat(this.fechaActual);
		let titulo = `Acciones Comerciales ${this.dateToStringDateTitleExcelFormat(new Date())}`;
		let fileName = `Reporte de Acciones Comerciales ${this.utilService.dateToStringDateFullFormat(new Date())}00`;
	
		this.excelService.generateExcel(
		  titulo,
		  headerExcel,
		  dataExcel,
		  fechaExcel,
		  fileName
		);
		this.cargador.hide();
	
		swal("Perfecto", "Su reporte ha sido generado con exito!", "success");
	  }



  // Funciones Auxiliares

  rutFormater(rut: string, digverificador: string) {
    return rut + "-" + digverificador;
  }

  // Thu Jul 18 2019 12:08:08 GMT-0400 (hora estándar de Chile) => 201907
  dateToStringDateTitleExcelFormat(date) {
    let anio = date.getFullYear();
    let mes = date.getMonth() + 1;

    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${anio}${mesStr}`;
    return fechaString;
  }

  // Thu Jul 18 2019 12:08:08 GMT-0400 (hora estándar de Chile) => 07
  dateToStringMonthFormat(date) {
    let mes = date.getMonth() + 1;
    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${mesStr}`;
    return fechaString;
  }

}
