	generateExcelEfectividades() {
		this.cargador.show();

		this.request.nrutoperador = Number(this.user.nrutoperador);
		this.request.sfhodesde = (this.forma3.controls.fechaDesde.value).replace(/-/g, '');
		this.request.sfhohasta = (this.utilService.dateToStringFormat(this.fechaActual)).replace(/-/g, '');

		this.ReportesService.getRptEfectividad(this.request)
			.subscribe((data: any) => {
				this.request = {};
				this.cargador.hide();
				if (data.rptEfectividad.length === 0 || data.rptEfectividad2.length === 0 || data.error.codError ===  500) {
					swal('Atención', 'No se encontraron resultados', 'info');
					} else {
						const auxDataPadre = data.rptEfectividad;

						const auxDataHijo = data.rptEfectividad2;

						const headerExcel = ['RECORRIDO', 'RAZON SOCIAL','DATOS'];

						for (let i = 1 ; i < 32; i++) {
							if (i < 10) {
								headerExcel.push('0' + i.toString());
							} else {
								headerExcel.push(i.toString());
							}
						}
						headerExcel.push('TOTAL GENERAL');

						// Construir Matriz de datos
						auxDataPadre.forEach(element => {
							let lista = [];
							lista = auxDataHijo.filter(item => item.rec_scodrecorrido === element.rec_scodrecorrido);
							element.lista  = lista;
						});


						const dataExcel = [];
						const TotalesPnv = ['','',''];
						const TotalesFacturas = ['','',''];
						const TotalesKilo = ['','',''];
						let totalPnvGeneral = 0;
						let totalFacturasGeneral = 0;
						let totalKilosGeneral = 0;
						const countDias = [];
						const countDias2 = [];
						const countDias3 = [];
						for (let j = 1; j < 32;j++) {
							TotalesPnv.push('0');
							TotalesFacturas.push('0');
							TotalesKilo.push('0');
							countDias.push(0);
							countDias2.push(0);
							countDias3.push(0);
						}

					 	for (const i in auxDataPadre) {
							let totalPnvIndiv = 0;
							let totalFacturasIndv = 0;
							let totalKilosIndv = 0;

							/* % PNV */
							let item = [
								auxDataPadre[i].rec_scodrecorrido,
								auxDataPadre[i].tra_srazonsocial,
								'% PNV'
							];

							for (let j = 1; j < 32;j++) {
								item.push('0');
							}

							auxDataPadre[i].lista.forEach(element => {
								const dia = element.dia + 2;
								item[dia] = element.kpi1;
								totalPnvIndiv += element.kpi1;

								TotalesPnv[dia] = (Number(TotalesPnv[dia]) + element.kpi1).toString();
							});
							totalPnvGeneral += totalPnvIndiv / auxDataPadre[i].lista.length;
							item.push(totalPnvIndiv / auxDataPadre[i].lista.length);

							dataExcel.push(item);
							/*FIN % PNV */
							/* % N° Facturas */
							item = [];
							item = [
								'',
								'',
								'% N° Facturas'
							];

							for (let j = 1; j < 32;j++) {
								item.push('0');
							}

							auxDataPadre[i].lista.forEach(element => {
								const dia = element.dia + 2;
								item[dia] = element.kpi2;
								totalFacturasIndv += element.kpi2;

								TotalesFacturas[dia] = (Number(TotalesFacturas[dia]) + element.kpi2).toString();
							});

							totalFacturasGeneral += totalFacturasIndv / auxDataPadre[i].lista.length;
							item.push(totalFacturasIndv / auxDataPadre[i].lista.length);
							dataExcel.push(item);
							/*FIN % N° Facturas */
							/* % Kilos */
							item = [];
							item = [
								'',
								'',
								'% Kilos'
							];

							for (let j = 1; j < 32;j++) {
								item.push('0');
							}

							auxDataPadre[i].lista.forEach(element => {
								const dia = element.dia + 2;
								item[dia] = element.kpi3;
								totalKilosIndv += element.kpi3;
								TotalesKilo[dia] = (Number(TotalesKilo[dia]) + element.kpi3).toString();
							});

							totalKilosGeneral += totalKilosIndv / auxDataPadre[i].lista.length;
							item.push(totalKilosIndv / auxDataPadre[i].lista.length);
							dataExcel.push(item);
							/*FIN % Kilos */
						}

						for (let i = 0; i < dataExcel.length; i = i + 3) {
							const auxLista = dataExcel[i];
							for (let j = 3; j < 34;j++) {
								if(auxLista[j] !== '') {
									countDias[j - 2]++;
								}
							}
						}
						for (let i = 1; i < dataExcel.length; i = i + 3) {
							const auxLista = dataExcel[i];
							for (let j = 3; j < 34;j++) {
								if(auxLista[j] !== '') {
									countDias2[j - 2]++;
								}
							}
						}
						for (let i = 2; i < dataExcel.length; i = i + 3) {
							const auxLista = dataExcel[i];
							for (let j = 3; j < 34;j++) {
								if(auxLista[j] !== '') {
									countDias3[j - 2]++;
								}
							}
						}
						for (let j = 3; j < 33;j++) {
							if(countDias[j - 2] !== 0) {
								TotalesPnv[j] = (Number(TotalesPnv[j]) / countDias[j - 2]).toString();
							}
							if(countDias2[j - 2] !== 0) {
								TotalesFacturas[j] = (Number(TotalesFacturas[j]) / countDias2[j - 2]).toString();
							}
							if(countDias3[j - 2] !== 0) {
								TotalesKilo[j] = (Number(TotalesKilo[j]) / countDias3[j - 2]).toString();
							}
						}
						// Totales
						TotalesPnv.push((totalPnvGeneral / auxDataPadre.length).toString());
						TotalesFacturas.push((totalFacturasGeneral / auxDataPadre.length).toString());
						TotalesKilo.push((totalKilosGeneral / auxDataPadre.length).toString());
						TotalesPnv[2] = 'Total % PNV';
						TotalesFacturas[2] = 'Total % N° Facturas';
						TotalesKilo[2] = 'Total % Kilos';
						/* <console.log(dataExcel);
						console.log(TotalesPnv);
						console.log(TotalesFacturas);
						console.log(TotalesKilo);> */
						dataExcel.push(TotalesPnv);
						dataExcel.push(TotalesFacturas);
						dataExcel.push(TotalesKilo);

						const fechaExcel = this.utilService.dateToLocalStringFormat(this.fechaActual);
						const nombreExcel = 'Gestion Comercial ' + this.utilService.dateToStringDateFullFormat(this.fechaActual);
						this.excelService.generateExcelConSubTitulo(nombreExcel, headerExcel, dataExcel, fechaExcel, 'Reporte Efectividades', headerExcel.length);

						swal('Perfecto', 'Su reporte ha sido generado con exito!', 'success');

					}
			}),
			error => {
				this.cargador.hide();
				swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
				console.log('error en el request');
			};
	}
