import { Component, OnInit, OnDestroy } from '@angular/core';
import { registerLocaleData, Time } from '@angular/common';

import { AuthService } from '../../../core/consultaService/auth.service';
import { ReportesService } from './../../../core/consultaService/reportes.service';
import { UtilService } from '../../../services/util.service';
import { ExcelService } from '../../../core/excel/excel.service';

import es from '@angular/common/locales/es';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $: any;
const swal = require('sweetalert');

@Component({
	selector: 'app-consolidado-ctas-clientes',
	templateUrl: './consolidadoCtasClientes.component.html',
	styleUrls: ['./consolidadoCtasClientes.component.css']
})

export class ConsolidadoCtasClientesComponent implements OnInit {

	request: any = {};

    user: any = {};
    
	subData: any;
	
	fechaActual = new Date();

	dataOtrosMontos: Array<any> = [];

	dataTramos: Array<any> = [];

	nDiasCartera: any = 0;
	
	nDiasCarteraCorp: any = 0;
	
	nDiasCarteraDivi: any = 0;
	
	nMontoPorVencer: any = 0;
	
	nMontoPorVencerCor: any = 0;
	
	nMontoPorVencerDiv: any = 0;

	totalCorporativo: any = 0;
	
	totalDivisional: any = 0;

	totalGeneral: any = 0;

	dataReporte: Array<any> = [];

	totalT1: any = 0;
	totalT2: any = 0;
	totalT3: any = 0;
	totalT4: any = 0;
	totalT5: any = 0;
	totalT6: any = 0;
	totalT7: any = 0;
	totalT8: any = 0;
	
	totalGeneralOtrosMontos: any = 0;
	totalesPorTipo: Array<any> = [];
					
	valido: Boolean = true;
        
	constructor(
		private authService: AuthService,
		private reportesService: ReportesService,
		private utilService: UtilService,
		private excelService: ExcelService,
		public cargador: NgxSpinnerService) {}

	ngOnInit() {
		this.user = this.authService.getDatosUser();
		registerLocaleData(es);
		this.initData();
	}

	initData() {
		this.cargador.show();
		this.request.nRutOperador = Number(this.user.nrutoperador);
		this.reportesService.getConsolidadoCta(this.request)
			.subscribe((data: any) => {
				this.request = {};
				this.cargador.hide();
				if (data.montos.length == 0 || data.otrosMontos.length == 0 || data.tramos.length == 0  && data.error.codError == 0) {
					swal("Atención", "No se encontraron resultados", "info");
					this.valido = true;
					} else {
						this.valido = false;
						const auxDataMontos = data.montos;
						this.dataOtrosMontos = data.otrosMontos;
						this.dataTramos = data.tramos;
						
						this.nDiasCartera = data.nDiasCartera;
						this.nDiasCarteraCorp = data.nDiasCarteraCorp;
						this.nDiasCarteraDivi = data.nDiasCarteraDivi;
						this.nMontoPorVencer = data.nMontoPorVencer;
						this.nMontoPorVencerCor = data.nMontoPorVencerCor;
						this.nMontoPorVencerDiv = data.nMontoPorVencerDiv;


						for (let i in this.dataTramos) {
							this.dataTramos[i].totalCorporativoPorDias = 0;
							this.dataTramos[i].totalDivisionalPorDias = 0;
							for (let j in auxDataMontos) {
								if (this.dataTramos[i].par_numero01 >= auxDataMontos[j].dias && auxDataMontos[j].dias <= this.dataTramos[i].par_numero02) {
									this.dataTramos[i].totalCorporativoPorDias += auxDataMontos[j].corporativo;
									this.dataTramos[i].totalDivisionalPorDias += auxDataMontos[j].divisional;
								} 
							}	
						}	
						this.totalCorporativo = 0;
						this.totalDivisional = 0;
						for (let i in this.dataTramos) {
							this.totalCorporativo += this.dataTramos[i].totalCorporativoPorDias;
							this.totalDivisional += this.dataTramos[i].totalDivisionalPorDias;
						}
						this.totalGeneral += this.totalCorporativo + this.nMontoPorVencerCor + this.totalDivisional + this.nMontoPorVencerDiv;
						console.log(this.dataTramos);
						
						for (let i in this.dataOtrosMontos) {

							this.totalT1 += this.dataOtrosMontos[i].rg1;
							this.totalT2 += this.dataOtrosMontos[i].rg2;
							this.totalT3 += this.dataOtrosMontos[i].rg3;
							this.totalT4 += this.dataOtrosMontos[i].rg4;
							this.totalT5 += this.dataOtrosMontos[i].rg5;
							this.totalT6 += this.dataOtrosMontos[i].rg6;
							this.totalT7 += this.dataOtrosMontos[i].rg7;
							this.totalT8 += this.dataOtrosMontos[i].rg8;
							console.log(this.totalT1);

							this.totalGeneralOtrosMontos += this.totalT1 + this.totalT2 + this.totalT3 + this.totalT4 + this.totalT5 +this.totalT6+ this.totalT7 + this.totalT8;
							const aux =  this.totalT1 + this.totalT2 + this.totalT3 + this.totalT4 + this.totalT5 +this.totalT6+ this.totalT7 + this.totalT8;
							this.totalesPorTipo.push(aux); 
						}
						console.log(this.totalesPorTipo);
					}
			}),
			error => {
				this.cargador.hide();
				swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
				console.log('error en el request');
			}	};
			generateExcel() {
			
			const parametros = {
								dataOtrosMontos: this.dataOtrosMontos,
								dataTramos: this.dataTramos,
								nDiasCartera: this.nDiasCartera,
								nDiasCarteraCorp: this.nDiasCarteraCorp,
								nDiasCarteraDivi: this.nDiasCarteraDivi,
								nMontoPorVencer: this.nMontoPorVencer,
								nMontoPorVencerCor: this.nMontoPorVencerCor,
								nMontoPorVencerDiv: this.nMontoPorVencerDiv,
								totalCorporativo: this.totalCorporativo,
								totalDivisional: this.totalDivisional,
								totalGeneral: this.totalGeneral,
								totalT1: this.totalT1,
								totalT2: this.totalT2,
								totalT3: this.totalT3,
								totalT4: this.totalT4,
								totalT5: this.totalT5,
								totalT6: this.totalT6,
								totalT7: this.totalT7,
								totalT8: this.totalT8,
								totalGeneralOtrosMontos: this.totalGeneralOtrosMontos,
								totalesPorTipo: this.totalesPorTipo,
								}
		
			let fechaExcel = this.utilService.dateToLocalStringFormat(this.fechaActual);
			let nombreExcel ='Consolidado Cuenta Corriente' + this.utilService.dateToStringDateFullFormat(this.fechaActual);
			this.excelService.generateExcelConsolidoCta(
					nombreExcel,
				    'Reporte Consolidado Cuenta Corriente',
					parametros,
					fechaExcel);
			
			swal('Perfecto', 'Su reporte ha sido generado con exito!', 'success');				

			}	
}
