import { Injectable } from "@angular/core";
import { Workbook } from "exceljs/dist/exceljs.min";
import * as fs from "file-saver";
import * as logoFile from "./logo.js";

@Injectable({
  providedIn: "root"
})
export class ExcelRDPService {
  constructor() {}
  generateExcel(
    title: string,
    headerExcel: any,
    dataExcel: any,
    totalDifPrecio: number,
    fechaExcel: string,
    snombreoperador: string,
    periodo: string,
    fileName: string
  ) {
    //Titulo, Encabezado y Datos

    //Creacion del libro y las hojas
    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet(title);

 

    //Agrega filas y su formato
    let titleRow = worksheet.addRow([title]);
    titleRow.font = {
      name: "Calibri",
      family: 4,
      size: 20,
      underline: "double",
      bold: true
    };
    worksheet.addRow([]);

    worksheet.getCell("B1").value = "Reporte Diferencia de Precios";
    worksheet.mergeCells("B1:I3");
    worksheet.getCell("B1").alignment = {
      vertical: "middle",
      horizontal: "center"
    };

    //Add Image
    let logo = workbook.addImage({
      base64: logoFile.logoBase64,
      extension: "png"
    });
    worksheet.addImage(logo, "A1:A3");

    //Fila de separacion (en blanco)
    worksheet.addRow([]);

    let infoRow = worksheet.addRow();
    worksheet.getCell(`A${infoRow.number}`).value = "Distribuidor:";
    worksheet.getCell(`B${infoRow.number}`).value = snombreoperador;
    worksheet.mergeCells(`B${infoRow.number}:E${infoRow.number}`);
    worksheet.getCell(`F${infoRow.number}`).value = "Periodo:";
    worksheet.mergeCells(`F${infoRow.number}:G${infoRow.number}`);
    worksheet.getCell(`H${infoRow.number}`).value = periodo;
    worksheet.mergeCells(`H${infoRow.number}:I${infoRow.number}`);
    infoRow.getCell(1).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };
    infoRow.getCell(6).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };
    infoRow.eachCell((cell, number) => {
      cell.border = {
        top: { style: "thin" },
        left: { style: "thin" },
        bottom: { style: "thin" },
        right: { style: "thin" }
      };
    });

    //Fila de separacion (en blanco)
    worksheet.addRow([]);

    //Add Header Row
    let headerRow = worksheet.addRow(headerExcel);
    headerRow.alignment = { vertical: 'middle', horizontal: 'center' };

    // Estilo de celda : Fill and Border
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFB0C4DE" },
        bgColor: { argb: "" }
      };
      cell.border = {
        top: { style: "thin" },
        left: { style: "thin" },
        bottom: { style: "thin" },
        right: { style: "thin" }
      };
    });
    worksheet.getCell("B1").alignment = {
      vertical: "middle",
      horizontal: "center"
    };

    worksheet.addRows(dataExcel);
    worksheet.getColumn(1).width = 30;
    worksheet.getColumn(2).width = 10;
    worksheet.getColumn(3).width = 55;
    worksheet.getColumn(4).width = 12;
    worksheet.getColumn(5).width = 14;
    worksheet.getColumn(6).width = 13;
    worksheet.getColumn(7).width = 13;
    worksheet.getColumn(8).width = 10;
    worksheet.getColumn(9).width = 12;

    worksheet.addRow([]);
    let totalDifPrecioRow = worksheet.addRow([]);
    worksheet.getCell(`F${totalDifPrecioRow.number}`).value = "Total:";
    worksheet.mergeCells(
      `F${totalDifPrecioRow.number}:G${totalDifPrecioRow.number}`
    );
    worksheet.getCell(`H${totalDifPrecioRow.number}`).value = totalDifPrecio;
    worksheet.mergeCells(
      `H${totalDifPrecioRow.number}:I${totalDifPrecioRow.number}`
    );
    totalDifPrecioRow.getCell(6).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };
    totalDifPrecioRow.getCell(6).border = {
      top: { style: "thin" },
      left: { style: "thin" },
      bottom: { style: "thin" },
      right: { style: "thin" }
    };
    totalDifPrecioRow.getCell(8).border = {
      top: { style: "thin" },
      left: { style: "thin" },
      bottom: { style: "thin" },
      right: { style: "thin" }
    };

    //Pie de pagina
    let footerRow = worksheet.addRow(["2019-Disvet"]);
    footerRow.getCell(1).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };
    footerRow.getCell(1).border = {
      top: { style: "thin" },
      left: { style: "thin" },
      bottom: { style: "thin" },
      right: { style: "thin" }
    };

    //Merge Cells
    worksheet.mergeCells(`A${footerRow.number}:I${footerRow.number}`);

    worksheet.eachRow(function(row, rowNumber) {
      row.eachCell(function(cell, colNumber) {
        cell.border = {
          top: { style: "thin" },
          left: { style: "thin" },
          bottom: { style: "thin" },
          right: { style: "thin" }
  
        };
      });
    });


    if (fileName) {
      var exportName = `${fileName}.xlsx`;
    } else {
      var exportName = `${title}.xlsx`;
    }

    workbook.xlsx.writeBuffer().then((data: any) => {
      let blob = new Blob([data], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      });
      fs.saveAs(blob, exportName);
    });
  }
}
