import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators, FormControl } from "@angular/forms";

import { AuthService } from "../../../core/consultaService/auth.service";
import { ExcelRLCService } from "../../../core/excel/excelRLC.service";
import { ReportesService } from "./../../../core/consultaService/reportes.service";
import { UtilService } from "../../../services/util.service";
import { NgxSpinnerService } from "ngx-spinner";
const swal = require("sweetalert");

@Component({
  selector: "app-listado-recorrido",
  templateUrl: "./listadoRecorrido.component.html"
})
export class listadoRecorridoComponent implements OnInit {
  request: any = {};
  forma: FormGroup;
  fechaActual = new Date();
  user: any = {};

  meses: Array<any> = [
    { codMes: "01", descMes: "Enero" },
    { codMes: "02", descMes: "Febrero" },
    { codMes: "03", descMes: "Marzo" },
    { codMes: "04", descMes: "Abril" },
    { codMes: "05", descMes: "Mayo" },
    { codMes: "06", descMes: "Junio" },
    { codMes: "07", descMes: "Julio" },
    { codMes: "08", descMes: "Agosto" },
    { codMes: "09", descMes: "Septiembre" },
    { codMes: "10", descMes: "Octubre" },
    { codMes: "11", descMes: "Noviembre" },
    { codMes: "12", descMes: "Diciembre" }
  ];

  anios: Array<any> = [];
  Anio: any;
  Mes: any;

  constructor(
    public cargador: NgxSpinnerService,
    private authService: AuthService,
    private excelService: ExcelRLCService,
    private utilService: UtilService,
    private reportesService: ReportesService
  ) {
    this.forma = new FormGroup({
      mes: new FormControl("", Validators.required),
      anio: new FormControl("", Validators.required)
    });
  }

  ngOnInit() {
    this.setGenerar();
  }

  setGenerar() {
    this.cargaComboAnios();
    this.forma.controls.mes.setValue(
      this.utilService.dateToStringMonthFormat(this.fechaActual)
    );
    this.forma.controls.anio.setValue(this.fechaActual.getFullYear());
  }

  cargaComboAnios() {
    for (let i = 1900; i <= 2100; i++) {
      let anio = {
        codAnio: i,
        descAnio: `${i}`
      };
      this.anios.push(anio);
    }
  }

  generar() {
    this.cargador.show();

    this.user = this.authService.getDatosUser();
    this.request.nrutoperador = Number(this.user.nrutoperador);

    this.Anio = this.forma.controls.anio.value;
    this.Mes = this.forma.controls.mes.value;
    let fecha = this.Anio + this.Mes;
    this.request.sfecha = fecha;

    this.listadoRecorridos();
  }

  listadoRecorridos() {
    this.reportesService.getDataListadoRecorridos(this.request).subscribe(
      (data: any) => {
        this.cargador.hide();
        if (data.listadoRecorridos.length === 0 && data.error.codError === 0) {
          swal("Atención", "No se encontraron resultados", "info");
        } else {
          if (data.listadoRecorridos === null) {
            swal("Atención", "Ha ocurrido un error inesperado!", "error");
          } else {
            this.generateExcel(data.listadoRecorridos);
          }
        }
        if (data.error.codError !== 0) {
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        }
      },
      error => {
        this.cargador.hide();
        swal("Atención", "Ha ocurrido un error inesperado!", "error");
      }
    );
  }

  generateExcel(data: Array<any>) {
    const headerExcel = [
      "Nº",
      "Dia",
      "Fecha",
      "Nº Recorrido",
      "Localidad",
      "Transporte",
      "Desde",
      "Hasta",
      "Valor",
      "Estado",
      "Fecha",
      "Valor",
      "Diferencia"
    ];
    const dataExcel = [];

    let totalTotal = 0;
    let totalMontoPago = 0;

    data.forEach((e, index) => {
      totalTotal += e.ntotal;
      totalMontoPago += e.nmontopago;

      dataExcel.push([
        index + 1,
        this.utilService.dayOfWeek(e.rec_dfhorecorrido),
        this.utilService.dateStringServiceToReverseStringFormat(
          e.rec_dfhorecorrido
        ),
        e.rec_nidrecorrido,
        e.rec_sdesrecorrido,
        e.tra_srazonsocial,
        this.utilService.formatDotNumber(e.nnumfacturadesde),
        this.utilService.formatDotNumber(e.nnumfacturahasta),
        this.utilService.formatDotNumber(e.ntotal),
        e.rec_scodestado,
        e.fechaliquidacion !== " "
          ? this.utilService.dateStringServiceToReverseStringFormat(
              e.fechaliquidacion
            )
          : "",
        this.utilService.formatDotNumber(e.nmontopago),
        this.utilService.formatDotNumber(e.nmontopago - e.ntotal)
      ]);
    });

    let fechaExcel = this.utilService.dateToStringDateBarFormat(
      this.fechaActual
    );
    let titulo = `Listado Recorridos ${this.utilService.dateToStringDateTitleExcelFormat(
      new Date()
    )}`;
    let fileName = `Listado Recorridos ${this.utilService.dateToStringDateFullFormat(
      new Date()
    )}00`;
    let snombreoperador = this.user.snombreoperador;
    let totalDiferencia = totalMontoPago - totalTotal;

    this.excelService.generateExcel(
      titulo,
      headerExcel,
      dataExcel,
      fechaExcel,
      fileName,
      snombreoperador,
      this.utilService.formatDotNumber(totalTotal),
      this.utilService.formatDotNumber(totalMontoPago),
      this.utilService.formatDotNumber(totalDiferencia)
    );
    this.cargador.hide();

    swal("Perfecto", "Su reporte ha sido generado con exito!", "success");
  }
}
