import { Component, OnInit, OnDestroy } from "@angular/core";
import { FormGroup, Validators, FormControl } from "@angular/forms";

import { AuthService } from "../../../core/consultaService/auth.service";
import { ExcelRTTService } from "../../../core/excel/excelRTT.service";
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";

import { TransportistasPopUpComponent } from "../../../routes/commonPopUp/transportistasPopUp/transportistasPopUp.component";
import { AdministracionOperativaService } from "./../../../core/consultaService/administracionOperativa.service";
import { ReportesService } from "./../../../core/consultaService/reportes.service";
import { UtilService } from "./../../../services/util.service";

import { NgxSpinnerService } from "ngx-spinner";
const swal = require("sweetalert");
import * as _ from "lodash";


@Component({
  selector: "app-liquidacion",
  templateUrl: "./liquidacion.component.html"
})
export class LiquidacionComponent implements OnInit, OnDestroy {
  fechaActual = new Date();
  origen: string;

  bsModalRef: BsModalRef;
  transportistasModal: BsModalRef;
  vehiculosModal: BsModalRef;

  request: any = {};
  subData: any;
  forma: FormGroup;
  user: any = {};

  anios: Array<any> = [];
  liquidacionTransportistas: any = {};

  meses: Array<any> = [
    { codMes: "01", descMes: "Enero" },
    { codMes: "02", descMes: "Febrero" },
    { codMes: "03", descMes: "Marzo" },
    { codMes: "04", descMes: "Abril" },
    { codMes: "05", descMes: "Mayo" },
    { codMes: "06", descMes: "Junio" },
    { codMes: "07", descMes: "Julio" },
    { codMes: "08", descMes: "Agosto" },
    { codMes: "09", descMes: "Septiembre" },
    { codMes: "10", descMes: "Octubre" },
    { codMes: "11", descMes: "Noviembre" },
    { codMes: "12", descMes: "Diciembre" }
  ];
  Anio: any;
  Mes: any;
  nporcentaje_kp1: any;
  nbono_transportista: any;

  constructor(
    private authService: AuthService,
    private excelService: ExcelRTTService,
    private setearDatosTransportista: AdministracionOperativaService,
    private reportesService: ReportesService,
    public cargador: NgxSpinnerService,
    private utilService: UtilService,
    private modalService: BsModalService
  ) {
    this.forma = new FormGroup({
      transportista: new FormControl("", Validators.required),
      patente: new FormControl("", Validators.required),
      mes: new FormControl("", Validators.required),
      anio: new FormControl("", Validators.required)
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();

    this.subData = this.setearDatosTransportista.datos$.subscribe(resData => {
      this.setData(resData);
    });
    this.setGenerar();
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }

  setGenerar() {
    this.cargaComboAnios();
    this.forma.controls.mes.setValue(
      this.dateToStringMonthFormat(this.fechaActual)
    );
    this.forma.controls.anio.setValue(this.fechaActual.getFullYear());
    this.request.nruttransportista = 0;
  }

  cargaComboAnios() {
    for (let i = 1900; i <= 2100; i++) {
      let anio = {
        codAnio: i,
        descAnio: `${i}`
      };
      this.anios.push(anio);
    }
  }

  openModalTransportista() {
    this.origen = "transportistas";
    const initialState = {
      titulo: "Transportistas",
      origen: "transportistas",
      busquedaCompleta: true
    };
    this.transportistasModal = this.modalService.show(
      TransportistasPopUpComponent,
      { initialState, backdrop: "static", keyboard: false }
    );
  }

  openModalVehiculo() {
    this.origen = "vehiculo";
    const initialState = {
      titulo: "Vehiculo",
      origen: "vehiculo",
      busquedaCompleta: true,
      rutTransportista: this.request.nruttransportista
    };
    this.vehiculosModal = this.modalService.show(TransportistasPopUpComponent, {
      initialState,
      backdrop: "static",
      keyboard: false
    });
  }

  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case "transportistas": {
          this.request.nruttransportista = resData.tra_nruttransportista;
          this.forma.controls.transportista.setValue(resData.tra_srazonsocial);
          break;
        }

        case "vehiculo": {
          this.forma.controls.patente.setValue(resData.cam_spatente);

          break;
        }
      }
    }
  }

  generar() {
    this.cargador.show();
    this.request.nrutoperador = this.user.nrutoperador;
    this.request.spatente = this.forma.controls.patente.value;
    this.request.sfecha =
      this.forma.controls.anio.value + this.forma.controls.mes.value;

    if (this.validarEntradasFormularioTransportista()) {
      if(this.validarEntradasFormularioVehiculo()) {
        this.dataLiquidacionTransportista();
      }
    }  

  }

  validarEntradasFormularioTransportista(): boolean {
    let _validarEntradasFormulario = true;
    if (this.request.nruttransportista === 0 || this.request.nruttransportista === null) {
      swal("Atención", "Debe Seleccionar Transportista para generar el reporte", "error");
      _validarEntradasFormulario = false;
      this.cargador.hide();

    }
    return _validarEntradasFormulario;
  }

  validarEntradasFormularioVehiculo(): boolean {
    let _validarEntradasFormulario = true;
    if (this.request.spatente === "" || this.request.spatente === null) {
      swal("Atención", "Debe Seleccionar Vehículo para generar el reporte", "error");
      _validarEntradasFormulario = false;
      this.cargador.hide();
    }
    return _validarEntradasFormulario;
  }



  dataLiquidacionTransportista() {
    this.reportesService
      .getReporteLiquidacionTransportista(this.request)
      .subscribe(
        (data: any) => {
          this.cargador.hide();
          if (
            data.dataLiquidacionTransportista.length === 0 &&
            data.error.codError === 0
          ) {
            swal("Atención", "No se encontraron resultados", "info");
          } else {
            if (data.dataLiquidacionTransportista === null) {
              swal("Atención", "Ha ocurrido un error inesperado!", "error");
            } else {
              this.nporcentaje_kp1 = data.nporcentaje_kp1;
              this.nbono_transportista = data.nbono_transportista;
              this.generateExcel(data.dataLiquidacionTransportista);

            }
          }
          if (data.error.codError !== 0) {
            swal("Atención", "Ha ocurrido un error inesperado!", "error");
          }
        },
        error => {
          this.cargador.hide();
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        }
      );
  }

  generateExcel(data: Array<any>) {
    // ! Generacion del contenedor intermedio de data

    this.liquidacionTransportistas = {};

    for (let i in data) {
      let dia = data[i].rlt_dfhorecorrido;
      let recorrido =
        "(" + data[i].rlt_scodrecorrido + ")" + data[i].rlt_sdesrecorrido;

      if (!this.liquidacionTransportistas.hasOwnProperty(dia)) {
        let facturasDelDia = {
          dia: dia,
          Recorrido: recorrido,
          "Valor Fijo": "",
          Rutas: []
        };

        this.liquidacionTransportistas[dia] = facturasDelDia;
      }

      let facturasDespachadas =
        data[i].rlt_nentregaefectiva +
        data[i].rlt_nrechazosparciales +
        data[i].rlt_nrechazototal;

      let facturacionRuta = {
        Ruta: data[i].rlt_scodruta,
        "Facturas Despachadas":
          data[i].rlt_sdesruta + " (" + facturasDespachadas + " Facturas)",
        "Valor Fijo": data[i].rlt_ntarfijadiaria,
        "Valor Facturas Entregadas": data[i].rlt_ntarfactefectiva,
        "Valor Rechazos Parciales": data[i].rlt_ntarfactparcial,
        "Valor Rechazos Totales": data[i].rlt_ntarfactreachazada,
        "Contador Facturas Entregadas": data[i].rlt_nentregaefectiva,
        "Contador Rechazos Parciales": data[i].rlt_nrechazosparciales,
        "Contador Rechazos Totales": data[i].rlt_nrechazototal,
        facturasDespachadas: facturasDespachadas
      };

      this.liquidacionTransportistas[dia]["Rutas"].push(facturacionRuta);
    }

    let dataExcel = [];

    let sumatoriaMesValorFijo = 0;
    let sumatoriaMesFacturasEntregadas = 0;
    let sumatoriaMesRechazosParciales = 0;
    let sumatoriaMesRechazosTotales = 0;
    let totalAPagar = 0;

    for (let i in this.liquidacionTransportistas) {
      let rutas = [];
      let contadorFacturasDespachadas = 0;
      let contadorFacturasEntregadas = 0;
      let contadorRechazosParciales = 0;
      let contadorRechazosTotales = 0;

      let totalValorFijo = 0;
      let totalValorFacturasEntregadas = 0;
      let totalValorRechazosParciales = 0;
      let totalValorRechazosTotales = 0;

      for (let j in this.liquidacionTransportistas[i]["Rutas"]) {
        rutas.push(this.liquidacionTransportistas[i]["Rutas"][j].Ruta);
        contadorFacturasDespachadas =
          contadorFacturasDespachadas +
          this.liquidacionTransportistas[i]["Rutas"][j]["facturasDespachadas"];
        contadorFacturasEntregadas =
          contadorFacturasEntregadas +
          this.liquidacionTransportistas[i]["Rutas"][j][
            "Contador Facturas Entregadas"
          ];
        contadorRechazosParciales =
          contadorRechazosParciales +
          this.liquidacionTransportistas[i]["Rutas"][j][
            "Contador Rechazos Parciales"
          ];
        contadorRechazosTotales =
          contadorRechazosTotales +
          this.liquidacionTransportistas[i]["Rutas"][j][
            "Contador Rechazos Totales"
          ];

        totalValorFijo =
          totalValorFijo +
          this.liquidacionTransportistas[i]["Rutas"][j]["Valor Fijo"];
        totalValorFacturasEntregadas =
          totalValorFacturasEntregadas +
          this.liquidacionTransportistas[i]["Rutas"][j][
            "Valor Facturas Entregadas"
          ];
        totalValorRechazosParciales =
          totalValorRechazosParciales +
          this.liquidacionTransportistas[i]["Rutas"][j][
            "Valor Rechazos Parciales"
          ];
        totalValorRechazosTotales =
          totalValorRechazosTotales +
          this.liquidacionTransportistas[i]["Rutas"][j][
            "Valor Rechazos Totales"
          ];
      }

      let row = [];
      row[0] = this.liquidacionTransportistas[i].Recorrido;
      row[1] = rutas.join(" ");
      row[2] = contadorFacturasDespachadas + " Facturas Despachadas";
      row[3] = "";
      row[4] = contadorFacturasEntregadas + " Facturas";
      row[5] = contadorRechazosParciales + " Facturas";
      row[6] = contadorRechazosTotales + " Facturas";

      // ! Push de la linea cabecera del dia
      dataExcel.push(row);

      // ! Generacion de las lineas de facturacion por ruta
      for (let j in this.liquidacionTransportistas[i]["Rutas"]) {
        let rowFacturacion = [];
        rowFacturacion[0] = null;
        rowFacturacion[1] = null;
        rowFacturacion[2] = this.liquidacionTransportistas[i]["Rutas"][j][
          "Facturas Despachadas"
        ];
        rowFacturacion[3] = this.liquidacionTransportistas[i]["Rutas"][j][
          "Valor Fijo"
        ];
        rowFacturacion[4] = this.liquidacionTransportistas[i]["Rutas"][j][
          "Valor Facturas Entregadas"
        ];
        rowFacturacion[5] = this.liquidacionTransportistas[i]["Rutas"][j][
          "Valor Rechazos Parciales"
        ];
        rowFacturacion[6] = this.liquidacionTransportistas[i]["Rutas"][j][
          "Valor Rechazos Totales"
        ];

        dataExcel.push(rowFacturacion);
      }

      let rowSumatoria = [];
      rowSumatoria[0] = null;
      rowSumatoria[1] = null;
      rowSumatoria[2] =
        "Sumatoria dia: " +
        this.utilService.dateStringServiceToReverseStringFormat(
          this.liquidacionTransportistas[i]["dia"]
        );
      rowSumatoria[3] = totalValorFijo;
      rowSumatoria[4] = totalValorFacturasEntregadas;
      rowSumatoria[5] = totalValorRechazosParciales;
      rowSumatoria[6] = totalValorRechazosTotales;

      dataExcel.push(rowSumatoria);

      sumatoriaMesValorFijo = sumatoriaMesValorFijo + totalValorFijo;
      sumatoriaMesFacturasEntregadas =
        sumatoriaMesFacturasEntregadas + totalValorFacturasEntregadas;
      sumatoriaMesRechazosParciales =
        sumatoriaMesRechazosParciales + totalValorRechazosParciales;
      sumatoriaMesRechazosTotales =
        sumatoriaMesRechazosTotales + totalValorRechazosTotales;
      totalAPagar =
        sumatoriaMesValorFijo +
        sumatoriaMesFacturasEntregadas +
        sumatoriaMesRechazosParciales +
        sumatoriaMesRechazosTotales;
    }

    const headerExcel = [
      "Recorrido",
      "Rutas",
      "Facturas despachadas",
      "Valor Fijo",
      "Facturas Entregadas",
      "Rechazos Parciales",
      "Rechazos Totales"
    ];

    let titulo = `Tarifa Transportista ${this.utilService.dateToStringDateTitleExcelFormat(
      new Date()
    )}`;
    let fileName = `Reporte Tarifa Transportista ${this.utilService.dateToStringDateFullFormat(
      new Date()
    )}00`;

    let transportista = this.forma.controls.transportista.value;
    let patente = this.forma.controls.patente.value;

    let Anio = this.forma.controls.anio.value;
    let Mes = this.forma.controls.mes.value;

    let periodo = this.meses[Number(Mes) - 1].descMes + " - " + Anio;

    this.excelService.generateExcel(
      titulo,
      headerExcel,
      dataExcel,
      fileName,
      transportista,
      patente,
      periodo,
      sumatoriaMesValorFijo,
      sumatoriaMesFacturasEntregadas,
      sumatoriaMesRechazosParciales,
      sumatoriaMesRechazosTotales,
      this.nbono_transportista,
      totalAPagar
    );
    this.cargador.hide();

    swal("Perfecto", "Su reporte ha sido generado con exito!", "success");
  }

  // Thu Jul 18 2019 12:08:08 GMT-0400 (hora estándar de Chile) => 07
  dateToStringMonthFormat(date) {
    let mes = date.getMonth() + 1;
    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${mesStr}`;
    return fechaString;
  }
}
