import { Injectable } from "@angular/core";
import { Workbook } from "exceljs/dist/exceljs.min";
import * as fs from "file-saver";
import * as logoFile from "./logo.js";
import { AuthService } from "../consultaService/auth.service";

@Injectable({
  providedIn: "root"
})
export class ExcelRLPService {
  constructor(
    private authService: AuthService /* private datePipe: DatePipe */
  ) {}
  generateExcel(
    title: string,
    headerExcel: any,
    dataExcel: any,
    fechaExcel: string,
    fechaReporte: string,
    fileName: string
  ) {
    //Titulo, Encabezado y Datos

    //Creacion del libro y las hojas
    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet(title);

    worksheet.properties.defaultRowHeight = 15;

    //Agrega filas y su formato
    let titleRow = worksheet.addRow([title]);
    titleRow.font = {
      name: "Calibri",
      family: 4,
      size: 20,
      underline: "double",
      bold: true
    };
    worksheet.addRow([]);

    worksheet.getCell(
      "B1"
    ).value = this.authService.getDatosUser().snombreoperador;
    worksheet.mergeCells("B1:G3");
    worksheet.getCell('B1').alignment = { vertical: 'middle', horizontal: 'center' };


    //Add Image
    let logo = workbook.addImage({
      base64: logoFile.logoBase64,
      extension: "png"
    });
    worksheet.addImage(logo, "A1:A3");

    //Fila de separacion (en blanco)
    worksheet.addRow([]);

    worksheet.getCell("A4").value = "Reporte Lista de Precios";
    worksheet.mergeCells("A4:G6");
    worksheet.getCell("A4:G6").font = {
      name: "Calibri",
      family: 4,
      size: 20,
      underline: false,
      bold: true
    };
    worksheet.getCell('A4').alignment = { vertical: 'middle', horizontal: 'center' };


    //Fecha
    worksheet.addRow(["Fecha"]);
    worksheet.getCell("B7").value = fechaReporte;
    worksheet.mergeCells("B7:G7");
    worksheet.getCell('B7').alignment = { vertical: 'middle', horizontal: 'center' };

    //Fila de separacion (en blanco)
    worksheet.addRow([]);

    //Add Header Row
    let headerRow = worksheet.addRow(headerExcel);

    worksheet.getCell("A1:G10").alignment = {
      vertical: "middle",
      horizontal: "center"
    };

    // Estilo de celda : Fill and Border
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFB0C4DE" },
        bgColor: { argb: "" }
      };
    });

    headerRow.alignment = { vertical: "middle", horizontal: "center" };

    worksheet.addRows(dataExcel);

    worksheet.getColumn(1).width = 14;
    worksheet.getColumn(2).width = 45;
    worksheet.getColumn(3).width = 8;
    worksheet.getColumn(4).width = 9;
    worksheet.getColumn(5).width = 10;
    worksheet.getColumn(6).width = 13;
    worksheet.getColumn(7).width = 27;

    //Pie de pagina
    let footerRow = worksheet.addRow(["2019-Disvet"]);
    footerRow.getCell(1).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };


    //Merge Cells
    worksheet.mergeCells(`A${footerRow.number}:G${footerRow.number}`);

    worksheet.eachRow(function(row, rowNumber) {
      row.eachCell(function(cell, colNumber) {
        cell.border = {
          top: { style: "thin" },
          left: { style: "thin" },
          bottom: { style: "thin" },
          right: { style: "thin" }
        };
      });
    });

    //Generate Excel File with given name

    if (fileName) {
      var exportName = `${fileName}.xlsx`;
    } else {
      var exportName = `${title}.xlsx`;
    }

    workbook.xlsx.writeBuffer().then((data: any) => {
      let blob = new Blob([data], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      });
      fs.saveAs(blob, exportName);
    });
  }
}
