import { Component, OnInit, OnDestroy } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { ReportesService } from "../../../core/consultaService/reportes.service";

import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
import { NotaDeCreditoPopUpComponent } from "../../../routes/commonPopUp/notaDeCreditoPopUp/notaDeCreditoPopUp.component";

import { FormGroup, FormControl } from "@angular/forms";

import { ExcelNCDOService } from "../../../core/excel/excelNCDO.service";
import { UtilService } from "./../../../services/util.service";

import { NgxSpinnerService } from "ngx-spinner";

const swal = require("sweetalert");

@Component({
  selector: "app-notaCredito-descuento-omitido",
  templateUrl: "./notaCreditoDescuentoOmitido.component.html"
})
export class NotaCreditoDescuentoOmitidoComponent implements OnInit, OnDestroy {

  notaDeCreditoModal: BsModalRef;

  request: any = { parnumparametro: 41 };
  forma: FormGroup;
  user: any = {};
  subData: any;
  motivos;
  motivo;
  notasCreditos: Array<any> = [];
  fechaActual = new Date();
  buscaNotasCredito: any = {};
  requestdos: any = {};
  noteDetail: any = {};
  sumatoria: number = 0;
  pActualBuscaCliente = 1;
  meses = [
    { nombreMes: "Enero", numMes: "01" },
    { nombreMes: "Febrero", numMes: "02" },
    { nombreMes: "Marzo", numMes: "03" },
    { nombreMes: "Abril", numMes: "04" },
    { nombreMes: "Mayo", numMes: "05" },
    { nombreMes: "Junio", numMes: "06" },
    { nombreMes: "Julio", numMes: "07" },
    { nombreMes: "Agosto", numMes: "08" },
    { nombreMes: "Septiembre", numMes: "09" },
    { nombreMes: "Octubre", numMes: "10" },
    { nombreMes: "Noviembre", numMes: "11" },
    { nombreMes: "Diciembre", numMes: "12" }
  ];

  years = [
    "2002",
    "2003",
    "2004",
    "2005",
    "2006",
    "2007",
    "2008",
    "2009",
    "2010",
    "2011",
    "2012",
    "2013",
    "2014",
    "2015",
    "2016",
    "2017",
    "2018",
    "2019",
    "2020",
    "2021",
    "2022",
    "2023",
    "2024",
    "2025",
    "2026",
    "2027",
    "2028",
    "2029",
    "2030"
  ];

  actionBtns = false;
  notasForExcel: any;
  tiposNotas: string;
  origen: string;
  periodo: string;

  constructor(
    public cargador: NgxSpinnerService,
    private http: HttpClient,
    private reportesService: ReportesService,
    private excelService: ExcelNCDOService,
    public utilService: UtilService,
    public loader: NgxSpinnerService,
    private modalService: BsModalService,
  ) {
    this.forma = new FormGroup({
      mes: new FormControl(""),
      years: new FormControl(""),
      motives: new FormControl("")
    });
  }

  ncDescOmitRequest: any = {
    nrutoperador: 0,
    sfechareporte: "",
    smotivo: ""
  };

  year: string;
  month: string;
  monthAndYear: string;

  detalleNCRequest: any = {
    nIdNotaCredito: 0,
    nNumNotaCredito: 0,
    nRutOperador: 0
  };


  ngOnInit() {

    this.forma.controls.mes.setValue(this.dateToStringMonthFormat(this.fechaActual));
    this.forma.controls.years.setValue(String(this.fechaActual.getFullYear()));



    this.showMotivos();
    this.ncDescOmitRequest.nrutoperador = JSON.parse(
      localStorage.getItem("DatosUsuario")
    )["nrutoperador"];

    this.detalleNCRequest.nRutOperador = +JSON.parse(
      localStorage.getItem("DatosUsuario")
    )["nrutoperador"];
  }

  ngOnDestroy() {}

  showMotivos() {
    let headers = new HttpHeaders({
      "Content-Type": "application/json",
      Accept: "*/*"
    });
    return this.http
      .post(
        "http://10.185.204.122:8080/rest-apiDisvet-0.0.1/general/busca/parametros",
        {
          parnumparametro: 41
        },
        { headers }
      )
      .subscribe((data: any) => {
        this.motivos = data.general;
      });
  }

  //SETTING REQUEST OBJECT

  setFechaEmision() {
    this.ncDescOmitRequest.sfechareporte = `${this.year}${this.month}`;
  }

  setMonthNumber() {
    this.month = this.forma.controls.mes.value;
  }

  setYearNumber() {
    this.year = this.forma.controls.years.value;
  }

  sendCodigoMotivo() {
    this.ncDescOmitRequest.smotivo = this.forma.controls.motives.value;
  }

  // BUSCAR FACTURAS

  buscar() {
    this.loader.show();
    this.month = this.forma.controls.mes.value;  
    this.year = this.forma.controls.years.value;
  
    this.periodo = this.meses[Number(this.month) - 1].nombreMes + " - " + this.year
    console.log("this.periodo", this.periodo);
    
    this.setFechaEmision();
    this.sumatoria = 0;

    this.reportesService
      .getNcDescuentoOmitido(this.ncDescOmitRequest)
      .subscribe(
        (data: any) => {
          this.loader.hide();
          if (data.descuentoOmitidoDTO === null) {
            swal("Atención", "Ha ocurrido un error inesperado!", "error");
          } else {
            if (
              data.descuentoOmitidoDTO.length === 0 &&
              data.error.codError === 0
            ) {
              swal("Atención", "No se encontraron resultados", "info");
            } else {
              this.notasCreditos = data.descuentoOmitidoDTO;
              this.actionBtns = true;
              this.cargarData(data.descuentoOmitidoDTO);
              this.notasCreditos.forEach(item => {
                this.sumatoria += item.ncr_ntotal;
              });
            }
          }
        },
        error => {
          this.loader.hide();
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        }
      );



  }

  cargarData(data: Array<any>) {
    this.notasForExcel = [];

    for (let i in data) {
      this.tiposNotas = "";

      let notasForExcel = {
        ncr_nrutcliente: data[i].ncr_nrutcliente,
        ncr_nidnotacredito: data[i].ncr_nidnotacredito,
        ncr_sapematerno: data[i].ncr_sapematerno,
        ncr_nnumlocal: data[i].ncr_nnumlocal,
        ncr_dfhoemision: data[i].ncr_dfhoemision,
        ncr_sapepaterno: data[i].ncr_sapepaterno,
        loc_snombre: data[i].loc_snombre,
        ncr_sdigverificador: data[i].ncr_sdigverificador,
        ncr_nnumnotacredito: data[i].ncr_nnumnotacredito,
        ncr_snombre: data[i].ncr_snombre,
        ncr_ntotal: data[i].ncr_ntotal,
        tipoPedidoModif: this.tiposNotas
      };

      this.notasForExcel.push(notasForExcel);
    }
  }

  generateExcel() {
    let headerExcel = [
      "Fecha de emisión",
      "N° Nota Crédito",
      "Rut Cliente",
      "Cliente",
      "Local",
      "Monto"
    ];

    let dataExcel = [];
    for (let i in this.notasForExcel) {
      let notaCredito = [
        this.notasForExcel[i].ncr_dfhoemision,
        this.notasForExcel[i].ncr_nnumnotacredito,
        this.utilService.formatDotNumber(this.notasForExcel[i].ncr_nrutcliente) + "-" + this.notasForExcel[i].ncr_sdigverificador,
        this.notasForExcel[i].ncr_snombre,
        "(" + this.notasForExcel[i].ncr_nnumlocal + ") " + this.notasForExcel[i].loc_snombre,
        this.notasForExcel[i].ncr_ntotal
      ];
      dataExcel.push(notaCredito);
    }

    let titulo = `NC Descuento Omitido ${this.utilService.dateToStringDateTitleExcelFormat(new Date())}`;
    let fileName = `Reporte NC por Descuento Omitido ${this.utilService.dateToStringDateFullFormat(new Date())}00`;

    this.excelService.generateExcel(
      titulo,
      headerExcel,
      dataExcel,
      this.utilService.formatDotNumber(this.sumatoria),
      this.periodo,
      fileName
    );
    swal("Perfecto", "Su reporte ha sido generado con exito!", "success");
  }


  openModalnotaDeCredito(nota: any) {
    this.request = {};

    this.origen = "notaDeCredito";

    this.request.nIdNotaCredito = nota.ncr_nidnotacredito;
    this.request.nNumNotaCredito = nota.ncr_nnumnotacredito;

    const initialState = {
      titulo: "Detalle Nota Credito",
      origen: "notaDeCredito",
      request: this.request
    };

    this.notaDeCreditoModal = this.modalService.show(NotaDeCreditoPopUpComponent, { initialState, backdrop: "static", keyboard: false,
    class: 'big-model-dialog' });
  }




  dateToStringMonthFormat(date) {
    let mes = date.getMonth() + 1;
    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${mesStr}`;
    return fechaString;
  }


}
