import { ReportesService } from "./../../../core/consultaService/reportes.service";
import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators, FormControl } from "@angular/forms";

import { ExcelRLPService } from "../../../core/excel/excelRLP.service";
import { UtilService } from "./../../../services/util.service";
import { NgxSpinnerService } from "ngx-spinner";

const swal = require("sweetalert");

@Component({
  selector: "app-listado-precios",
  templateUrl: "./listadoPrecios.component.html"
})
export class ListadoPreciosComponent implements OnInit {
  fechaActual = new Date();
  fechaActualFormGroup = new Date(new Date().getTime() - (new Date().getTimezoneOffset() * 60000)).toISOString().split("T")[0];
  request: any = {};

  categorias: Array<any> = [
    { scodcatnegocio: "", can_sdescatnegocio: "Seleccione ..." },
    { scodcatnegocio: "67", can_sdescatnegocio: "ATC" },
    { scodcatnegocio: "17", can_sdescatnegocio: "ALMACEN A" },
    { scodcatnegocio: "94", can_sdescatnegocio: "ALMACEN B" },
    { scodcatnegocio: "95", can_sdescatnegocio: "AMBULANTES" },
    { scodcatnegocio: "96", can_sdescatnegocio: "CANALES ESPECIALIZADOS" },
    { scodcatnegocio: "97", can_sdescatnegocio: "CASH" },
    { scodcatnegocio: "62", can_sdescatnegocio: "COLEGIOS/UNIVERSIDADES" },
    { scodcatnegocio: "5", can_sdescatnegocio: "DISTRIBUIDORES" },
    { scodcatnegocio: "24", can_sdescatnegocio: "FARMACIAS" },
    { scodcatnegocio: "89", can_sdescatnegocio: "FERIA LIBRE" },
    { scodcatnegocio: "26", can_sdescatnegocio: "KIOSCOS" },
    { scodcatnegocio: "4", can_sdescatnegocio: "MAYORISTAS" },
    { scodcatnegocio: "3", can_sdescatnegocio: "MINI MARKET" },
    { scodcatnegocio: "92", can_sdescatnegocio: "MINI MARKET B" },
    { scodcatnegocio: "21", can_sdescatnegocio: "PANADERIAS Y/O PASTELERIAS" },
    { scodcatnegocio: "1", can_sdescatnegocio: "SUPERMERCADOS" },
    { scodcatnegocio: "15", can_sdescatnegocio: "TRANSPORTISTAS" }
  ];

  forma: FormGroup;

  constructor(
    private excelService: ExcelRLPService,
    public cargador: NgxSpinnerService,
    private reportesService: ReportesService,
    private utilService: UtilService
  ) {
    this.forma = new FormGroup({
      categoria: new FormControl("", Validators.required),
      fecha: new FormControl("", Validators.required)
    });
  }

  ngOnInit() {
    this.setGenerar();
  }

  setGenerar() {
    this.forma.controls.fecha.setValue(this.utilService.dateToStringDateRequestFullFormat(new Date()));
    this.forma.controls.categoria.setValue(this.categorias[0].scodcatnegocio);
  }

  onFechaChange(event: any) {
    this.forma.patchValue({ fecha: event.target.value });
  }

  generar() {
    this.cargador.show();

    this.request.scodcatnegocio = this.forma.controls.categoria.value;
    this.request.sestado = "";
    this.request.sfecha = this.utilService.stringFormatSinSeparador(
      this.forma.controls.fecha.value
    );
    this.listadoPrecios();
  }

  listadoPrecios() {
    this.reportesService.getRptListadoPrecios(this.request).subscribe(
      (data: any) => {
        this.cargador.hide();
        if (
          data.dataListadoRecorridos.length === 0 &&
          data.error.codError === 0
        ) {
          swal("Atención", "No se encontraron resultados", "info");
        } else {
          if (data.dataListadoRecorridos === null) {
            swal("Atención", "Ha ocurrido un error inesperado!", "error");
          } else {
            this.generateExcel(data.dataListadoRecorridos);
          }
        }
        if (data.error.codError !== 0) {
          swal("Atención", "Ha ocurrido un error inesperado!", "error");
        }
      },
      error => {
        this.cargador.hide();
        swal("Atención", "Ha ocurrido un error inesperado!", "error");
      }
    );
  }

  generateExcel(data: Array<any>) {
    const headerExcel = [
      "COD. Articulo",
      "Descripcion",
      "Estado",
      "Precio",
      "Descuento",
      "Precio Final",
      "Categoria"
    ];
    const dataExcel = [];

    data.forEach(e => {
      dataExcel.push([
        e.art_scodformato + "-" + e.art_scodvariedad,
        e.art_sdescripcion,
        e.art_sdesestado,
        e.pra_nprecio,
        e.def_nporcdescto,
        e.preciocondesc,
        e.can_sdescatnegocio
      ]);
    });


    let fechaExcel = this.utilService.dateToStringDateBarFormat(
      this.fechaActual
    );
    let fechaReporte = this.utilService.dateStringServiceToReverseStringFormat(this.request.sfecha);
    let titulo = `Lista de Precios ${this.dateToStringDateTitleExcelFormat(
      new Date()
    )}`;
    let fileName = `Reporte Lista de Precios ${this.utilService.dateToStringDateFullFormat(
      new Date()
    )}00`;

    this.excelService.generateExcel(
      titulo,
      headerExcel,
      dataExcel,
      fechaExcel,
      fechaReporte,
      fileName
    );
    this.cargador.hide();

    swal("Perfecto", "Su reporte ha sido generado con exito!", "success");
  }

  // Funciones Auxiliares

  // Thu Jul 18 2019 12:08:08 GMT-0400 (hora estándar de Chile) => 201907
  dateToStringDateTitleExcelFormat(date) {
    let anio = date.getFullYear();
    let mes = date.getMonth() + 1;

    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${anio}${mesStr}`;
    return fechaString;
  }
}
