import { Component, OnInit, OnDestroy } from "@angular/core";
import { FormGroup, Validators, FormControl } from "@angular/forms";

import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "../../../core/consultaService/auth.service";
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { AdministracionOperativaService } from './../../../core/consultaService/administracionOperativa.service';

import { ReportesService } from "./../../../core/consultaService/reportes.service";
import { ExcelRDPService } from "../../../core/excel/excelRDP.service";
import { UtilService } from "./../../../services/util.service";
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { OperadoresPopUpComponent } from '../../../routes/commonPopUp/operadoresPopUp/operadoresPopUp.component';


const swal = require("sweetalert");

@Component({
  selector: "app-diferencia-precio",
  templateUrl: "./diferenciaPrecio.component.html"
})
export class DiferenciaPrecioComponent implements OnInit, OnDestroy {

  fechaActual = new Date();
  bsModalRef: BsModalRef;
  operadoresUsuarioModal: BsModalRef;

  origen: string;
  
  request: any = {};
  operadores: Array<any> = [];

  forma: FormGroup;
  user: any = {};
  meses: Array<any> = [
    { codMes: "01", descMes: "Enero" },
    { codMes: "02", descMes: "Febrero" },
    { codMes: "03", descMes: "Marzo" },
    { codMes: "04", descMes: "Abril" },
    { codMes: "05", descMes: "Mayo" },
    { codMes: "06", descMes: "Junio" },
    { codMes: "07", descMes: "Julio" },
    { codMes: "08", descMes: "Agosto" },
    { codMes: "09", descMes: "Septiembre" },
    { codMes: "10", descMes: "Octubre" },
    { codMes: "11", descMes: "Noviembre" },
    { codMes: "12", descMes: "Diciembre" }
  ];

  anios: Array<any> = [];
  sNombreFantasiaDefault: any;
  nRutOperadorDefault: number;
  nRutOperador: any;
  Anio: any;
  Mes: any;
  productos: any;
  subData: any;
  subData2: any;
  ope_snombreDefault: any;
  periodo: string;

  constructor(
    private authService: AuthService,
    private setearDatos: ConsultasService,
    private usuariosService: AdministracionOperativaService,
    private reportesService: ReportesService,
    private excelService: ExcelRDPService,
    private utilService: UtilService,
    public cargador: NgxSpinnerService, 
    private modalService: BsModalService
  ) {
    this.forma = new FormGroup({
      nRutOperador: new FormControl('' , Validators.required),
      ope_snombre: new FormControl('' , Validators.required),
      mes: new FormControl("", Validators.required),
      anio: new FormControl("", Validators.required)
    });
  }

  ngOnInit() {

    this.subData = this.setearDatos.datosGeo$.subscribe(resData => {
      this.setData(resData);
    });
    this.subData2 = this.usuariosService.datos$.subscribe(resData => {
      this.setData(resData);
    });
    this.user = this.authService.getDatosUser();

    this.setGenerar();
  }

  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
    if (this.subData2 !== undefined) {
      this.subData2.unsubscribe();
    }
  }

  setGenerar() {
    this.cargaComboAnios();

    this.forma.controls.nRutOperador.setValue(Number(this.user.nrutoperador));

    this.request.nRutOperador = Number(this.user.nrutoperador);
    
    this.operadorComercialDefault();

    this.forma.controls.mes.setValue(this.dateToStringMonthFormat(this.fechaActual));
    this.forma.controls.anio.setValue(this.fechaActual.getFullYear());
  }

  cargaComboAnios() {
    for (let i = 1900; i <= 2100; i++) {
      let anio = {
        codAnio: i,
        descAnio: `${i}`
      };
      this.anios.push(anio);
    }
  }

  operadorComercialDefault() {
    this.usuariosService.getOperadoresUsuarios(this.request)
    .subscribe((data: any) => {
      this.cargador.hide();
      if (data.operadores.length === 0 && data.error.codError === 0 ) {
        swal('Atención', 'No se encontraron resultados', 'info');
      } else {
          if (data.operadores === null) {
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } else {
              this.forma.controls.nRutOperador.setValue(data.operadores[0].ope_nrutoperador);
              this.forma.controls.ope_snombre.setValue(data.operadores[0].ope_snombrefantasia);
          }
      }
      if (data.error.codError !== 0) {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      }
  },
  error => {
      this.cargador.hide();
      swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
  });

  }

  openModalOperadores() {
    this.origen = 'operadores';
    const initialState = {
      titulo: 'Operadores',
      origen: 'operadores',
      busquedaCompleta: true
    };
    this.operadoresUsuarioModal = this.modalService.show(OperadoresPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {

        case 'operadores': {
          this.forma.controls.nRutOperador.setValue(resData.ope_nrutoperador);
          this.forma.controls.ope_snombre.setValue(resData.ope_snombre);
          break;
        }
      }
    }
  }

  generar() {
    this.request = {};
    this.cargador.show();
    this.Anio = this.forma.controls.anio.value;
    this.Mes = this.forma.controls.mes.value;
    let fecha = this.Anio + this.Mes;

    this.periodo = this.meses[Number(this.Mes) - 1].descMes + " - " + this.Anio

    this.request.nrutoperador = this.forma.controls.nRutOperador.value;
    this.request.sfecha = fecha;

    this.operadoresComerciales();
  }

  operadoresComerciales() {
    this.reportesService.getReporteDiferenciaPrecio(this.request)
    .subscribe((data: any) => {
      this.cargador.hide();
      if (data.diferenciaPrecio.length === 0 && data.error.codError === 0 ) {
        swal('Atención', 'No se encontraron resultados', 'info');
      } else {
          if (data.diferenciaPrecio === null) {
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } else {
            this.generateExcel(data.diferenciaPrecio);
          }
      }
      if (data.error.codError !== 0) {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      }
  },
  error => {
      this.cargador.hide();
      swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
  });

  }

  generateExcel(data: Array<any>) {
    const headerExcel = [
      "Categoria",
      "Cod Sku",
      "Descripcion",
      "Costo FIFO",
      "Precio Venta",
      "PB Desde",
      "PB Hasta",
      "Cantidad",
      "Dif Precio"
    ];
    const dataExcel = [];
    let totalDifPrecio = 0;

    data.forEach(e => {
      dataExcel.push([
        e.jca_sdescategoria,
        this.skuFormater(e.art_scodformato, e.art_scodvariedad),
        e.art_sdescripcion,
        e.preciocostovta,
        e.preciobase,
        this.utilService.dateStringServiceToReverseStringFormat(e.pra_dfhoiniciovigencia),
        this.utilService.dateStringServiceToReverseStringFormat(e.pra_dfhofinvigencia),
        e.cantidadarticulos,
        e.diferenciaprecio
      ]);
      totalDifPrecio = totalDifPrecio + Math.round(e.diferenciaprecio)
    });

    let fechaExcel = this.utilService.dateToLocalStringFormat(this.fechaActual);
    let titulo = `Diferencia de Precios ${this.utilService.dateToStringDateTitleExcelFormat(new Date())}`;
    let fileName = `Reporte Diferencia de Precios ${this.utilService.dateToStringDateFullFormat(new Date())}00`;
    let snombreoperador = this.forma.controls.ope_snombre.value;

    this.excelService.generateExcel(
      titulo,
      headerExcel,
      dataExcel,
      totalDifPrecio,
      fechaExcel,
      snombreoperador,
      this.periodo,
      fileName
    );
    this.cargador.hide();

    swal("Perfecto", "Su reporte ha sido generado con exito!", "success");
  }

  // Funciones Auxiliares

  skuFormater(art_scodformato: string, art_scodvariedad: string) {
    return art_scodformato + "-" + art_scodvariedad;
  }

  // Thu Jul 18 2019 12:08:08 GMT-0400 (hora estándar de Chile) => 07
  dateToStringMonthFormat(date) {
    let mes = date.getMonth() + 1;
    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${mesStr}`;
    return fechaString;
  }
}
