import { ConsolidadoCtasClientesComponent } from "./consolidadoCtasClientes/consolidadoCtasClientes.component";
import { DiferenciaPrecioComponent } from "./diferenciaPrecio/diferenciaPrecio.component";
import { InformesDescuenstosComponent } from "./informesDescuenstos/informesDescuenstos.component";
import { LiquidacionComponent } from "./liquidacion/liquidacion.component";
import { ListadoPreciosComponent } from "./listadoPrecios/listadoPrecios.component";
