import { RelacionesComercialesPopUpComponent } from './../commonPopUp/relacionesComercialesPopUp/relacionesComercialesPopUp.component';
import { EfectividadFuerzaDeVentasComponent } from './efectividadFuerzaDeVentas/efectividadFuerzaDeVentas.component';
import { DocumentosSinFolioComponent } from './documentosSinFolio/documentosSinFolio.component';
import { AbastecimientoItemsCeroComponent } from './abastecimientoItemCero/abastecimientoItemCero.component';


const routes: Routes = [
  { path: "gestionComercial", component: GestionComercialComponent },
  { path: "accionesComerciales", component: AccionComercialesComponent },
  { path: "consolidadoCtasClientes", component: ConsolidadoCtasClientesComponent},
  { path: "diferenciaPrecio", component: DiferenciaPrecioComponent },
  { path: "informesDescuenstos", component: InformesDescuenstosComponent },
  { path: "liquidacion", component: LiquidacionComponent },
  { path: "listadoPrecios", component: ListadoPreciosComponent },
  { path: "motivoDevolucion", component: MotivoDevolucionComponent },
  { path: "notaCreditoDescuento",component: NotaCreditoDescuentoOmitidoComponent},
  { path: "piramidal", component: PiramidalComponent },
  { path: "listadoRecorrido", component: listadoRecorridoComponent },
  { path: "piramidalSimple", component: PiramidalSimpleComponent },
  { path: "accionesComerciales", component: AccionComercialesComponent },
  { path: 'efectividadFuerzaDeVentas', component: EfectividadFuerzaDeVentasComponent },
  { path: 'documentosSinFolio', component: DocumentosSinFolioComponent },
  { path: 'abastecimientoItemsCero', component: AbastecimientoItemsCeroComponent}


];

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild(routes),
    CommonModule,
    NgxPaginationModule,
    CommonPopUpModule
  ],
  declarations: [
    GenerarReporteComponent,
    GestionComercialComponent,
    AccionComercialesComponent,
    ConsolidadoCtasClientesComponent,
    DiferenciaPrecioComponent,
    InformesDescuenstosComponent,
    LiquidacionComponent,
    AbastecimientoItemsCeroComponent,
    ListadoPreciosComponent,
    MotivoDevolucionComponent,
    NotaCreditoDescuentoOmitidoComponent,
    PiramidalComponent,
    listadoRecorridoComponent,
    PiramidalSimpleComponent,
    EfectividadFuerzaDeVentasComponent,
    DocumentosSinFolioComponent
  ],
  entryComponents: [
    OperadoresPopUpComponent,
    TransportistasPopUpComponent,
    BuscarArticuloPopUpComponent,
    ZonasTodasPopUpComponent,
    NotaDeCreditoPopUpComponent,
    RelacionesComercialesPopUpComponent
  ]
})
export class ReporteModule { }
