import { Injectable } from "@angular/core";
import { Workbook } from "exceljs/dist/exceljs.min";
import * as fs from "file-saver";
import * as logoFile from "./logo.js";

@Injectable({
  providedIn: 'root'
})
export class ExcelNCDOService {
  constructor() {}
  generateExcel(
    title: string,
    headerExcel: any,
    dataExcel: any,
    total: number,
    periodo: string,
    fileName: string
  ) {

    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet(title);

    //Agrega filas y su formato
    let titleRow = worksheet.addRow([title]);
    titleRow.font = {
      name: "Calibri",
      family: 4,
      size: 20,
      underline: "double",
      bold: true
    };
    worksheet.addRow([]);

    worksheet.getCell("B1").value = "Reporte NC por Descuento Omitido";
    worksheet.mergeCells("B1:F3");
    worksheet.getCell("B1").alignment = {
      vertical: "middle",
      horizontal: "center"
    };

    //Add Image
    let logo = workbook.addImage({
      base64: logoFile.logoBase64,
      extension: "png"
    });
    worksheet.addImage(logo, "A1:A3");

    //Fila de separacion (en blanco)
    worksheet.addRow([]);

    let infoRow = worksheet.addRow();

    worksheet.getCell(`A${infoRow.number}`).value = "Periodo:";
    worksheet.getCell(`B${infoRow.number}`).value = periodo;
    infoRow.getCell(1).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };


    //Fila de separacion (en blanco)
    worksheet.addRow([]);

    //Add Header Row
    let headerRow = worksheet.addRow(headerExcel);
    headerRow.alignment = { vertical: 'middle', horizontal: 'center' };

    // Estilo de celda : Fill and Border
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFB0C4DE" },
        bgColor: { argb: "" }
      };
    });

    worksheet.addRows(dataExcel);
    worksheet.getColumn(1).width = 18;
    worksheet.getColumn(2).width = 18;
    worksheet.getColumn(3).width = 14;
    worksheet.getColumn(4).width = 50;
    worksheet.getColumn(5).width = 50;
    worksheet.getColumn(6).width = 14;

    worksheet.addRow([]);
    worksheet.addRow([]);

    let totalDifPrecioRow = worksheet.addRow([]);
    worksheet.getCell(`E${totalDifPrecioRow.number}`).value = "Total:";
    worksheet.getCell(`F${totalDifPrecioRow.number}`).value = "$ " + total;
    totalDifPrecioRow.getCell(5).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };


    //Pie de pagina
    let footerRow = worksheet.addRow(["2019-Disvet"]);
    footerRow.getCell(1).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };


    //Merge Cells
    worksheet.mergeCells(`A${footerRow.number}:F${footerRow.number}`);

    worksheet.eachRow(function(row, rowNumber) {
      row.eachCell(function(cell, colNumber) {
        cell.border = {
          top: { style: "thin" },
          left: { style: "thin" },
          bottom: { style: "thin" },
          right: { style: "thin" }
  
        };
      });
    });


    if (fileName) {
      var exportName = `${fileName}.xlsx`;
    } else {
      var exportName = `${title}.xlsx`;
    }

    workbook.xlsx.writeBuffer().then((data: any) => {
      let blob = new Blob([data], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      });
      fs.saveAs(blob, exportName);
    });
  }
}
