import { Injectable } from "@angular/core";
import { Workbook } from "exceljs/dist/exceljs.min";
import * as fs from "file-saver";
import * as logoFile from "./logo.js";
import { AuthService } from "../consultaService/auth.service";

@Injectable({
  providedIn: "root"
})
export class ExcelRPSService {
  constructor(
    private authService: AuthService /* private datePipe: DatePipe */
  ) {}
  generateExcel(
    title: string,
    headerExcel: any,
    dataExcel: any,
    dataTotales: any,
    fechaExcel: string,
    fileName?: string,
    columnas?: number
  ) {
    //Titulo, Encabezado y Datos

    //Creacion del libro y las hojas
    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet(title);

    //Agrega filas y su formato
    let titleRow = worksheet.addRow([title]);
    titleRow.font = {
      name: "Calibri",
      family: 4,
      size: 20,
      underline: "double",
      bold: true
    };
    worksheet.addRow([]);

    worksheet.getCell(
      "B1"
    ).value = this.authService.getDatosUser().snombreoperador;
    worksheet.mergeCells("B1:H3");
    worksheet.getCell("B1").alignment = {
      vertical: "middle",
      horizontal: "center"
    };

    //Add Image
    let logo = workbook.addImage({
      base64: logoFile.logoBase64,
      extension: "png"
    });
    worksheet.addImage(logo, "A1:A3");

    //Fila de separacion (en blanco)
    worksheet.addRow([]);

    //Fecha
    worksheet.addRow(["Fecha:"]);
    worksheet.getCell("B5").value = fechaExcel;


    //Fila de separacion (en blanco)
    worksheet.addRow([]);
    let infoRow = worksheet.addRow(["Reporte Piramidal Simple"]);
    infoRow.eachCell((cell, number) => {
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFB0C4DE" },
        bgColor: { argb: "" }
      };
    });
    worksheet.getCell("A7").alignment = {
      vertical: "middle",
      horizontal: "center"
    };

    var nColumnas: any = Number(columnas);


    worksheet.mergeCells(`A7:H7`);
    

    //Add Header Row
    let headerRow = worksheet.addRow(headerExcel);

    // Estilo de celda : Fill and Border
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFB0C4DE" },
        bgColor: { argb: "" }
      };
    });

    worksheet.addRows(dataExcel);



    worksheet.getColumn(1).width = 11;
    worksheet.getColumn(2).width = 45;
    worksheet.getColumn(3).width = 12;
    worksheet.getColumn(4).width = 12;
    worksheet.getColumn(5).width = 12;
    worksheet.getColumn(6).width = 12;
    worksheet.getColumn(7).width = 12;
    worksheet.getColumn(8).width = 12;
    worksheet.getColumn(9).width = 12;
    worksheet.getColumn(10).width = 12;
    worksheet.getColumn(11).width = 12;
    worksheet.getColumn(12).width = 12;
    worksheet.getColumn(13).width = 12;
    worksheet.getColumn(14).width = 12;
    worksheet.getColumn(15).width = 12;
    worksheet.getColumn(16).width = 12;
    worksheet.getColumn(17).width = 12;
    worksheet.getColumn(18).width = 12;
    worksheet.getColumn(19).width = 12;
    worksheet.getColumn(20).width = 12;
    worksheet.getColumn(21).width = 12;
    worksheet.getColumn(22).width = 12;
    worksheet.getColumn(23).width = 12;
    worksheet.getColumn(24).width = 12;
    worksheet.getColumn(25).width = 12;
    worksheet.getColumn(26).width = 12;




    let TotalesRow = worksheet.addRow(dataTotales);
    worksheet.getCell(`A${TotalesRow.number}`).value = "";
    worksheet.getCell(`B${TotalesRow.number}`).value = "TOTALES:";
    TotalesRow.eachCell((cell, number) => {
      cell.fill = {
        type: "pattern",
        pattern: "solid",
        fgColor: { argb: "FFB0C4DE" },
        bgColor: { argb: "" }
      };
    });

    //Pie de pagina
    let footerRow = worksheet.addRow(["2019-Disvet"]);
    footerRow.getCell(1).fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFB0C4DE" }
    };

    //Merge Cells
    worksheet.mergeCells(`A${footerRow.number}:D${footerRow.number}`);


    worksheet.eachRow(function(row, rowNumber) {
      row.eachCell(function(cell, colNumber) {
        cell.border = {
          top: { style: "thin" },
          left: { style: "thin" },
          bottom: { style: "thin" },
          right: { style: "thin" }
  
        };
      });
    });


    //Generate Excel File with given name

    if (fileName) {
      var exportName = `${fileName}.xlsx`;
    } else {
      var exportName = `${title}.xlsx`;
    }

    workbook.xlsx.writeBuffer().then((data: any) => {
      let blob = new Blob([data], {
        type:
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      });
      fs.saveAs(blob, exportName);
    });
  }
}
