import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';

import { AuthService } from '../../../core/consultaService/auth.service';
import { ExcelService } from '../../../core/excel/excel.service';
import { UtilService } from '../../../services/util.service';
import { ConsultasService } from './../../../core/consultaService/consultas.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { ReportesService } from './../../../core/consultaService/reportes.service';

import { NgxSpinnerService } from 'ngx-spinner';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { BuscarArticuloPopUpComponent } from '../../commonPopUp/buscarArticuloPopUp/buscarArticuloPopUp.component';

import es from '@angular/common/locales/es';

declare var $: any;
const swal = require('sweetalert');

@Component({
	selector: 'app-abastecimiento-items-cero',
	templateUrl: './abastecimientoItemCero.component.html'
})

export class AbastecimientoItemsCeroComponent implements OnInit, OnDestroy {

	fechaActual = new Date();
	request: any = {};
  
	forma: FormGroup;
	user: any = {};
	subData: any;
    datos: Array<any> = [];
    bsModalRef: BsModalRef;
    origen = '';

        
	constructor(
		public cargador: NgxSpinnerService, 
		private authService: AuthService, 
		private excelService: ExcelService,
		private utilService: UtilService,
		private consultasService: ConsultasService, 
		private modalService: BsModalService,
		private articuloService: ArticuloService,
		private reportesService: ReportesService,
		private administracionOperativaServiceDatos: AdministracionOperativaService) {

            this.forma = new FormGroup({
                fecha: new FormControl({ value: ''}, Validators.required),
                descLinea: new FormControl({value: '', disabled: true }, Validators.required),
                codLinea: new FormControl(''),
                descCategoria: new FormControl({value: '', disabled: true }, Validators.required),
                codCategoria: new FormControl(''),
                descAgrupacion: new FormControl({value: '', disabled: true }, Validators.required),
                codAgrupacion: new FormControl(''),
                descProdBase: new FormControl({value: '', disabled: true }, Validators.required),
                codProdBase: new FormControl('')
            });
	}

	ngOnInit() {
          this.user = this.authService.getDatosUser();
          this.initData();
    	  this.subData = this.consultasService.datos$.subscribe(resData =>{
			this.setData(resData);
		  });
    }
    
    ngOnDestroy() {
		if (this.subData !== undefined) {
			this.subData.unsubscribe();
		  }
    }
    initData(){
		this.forma.controls.fecha.setValue(this.utilService.dateToStringFormat(this.fechaActual));
	}

	setData(resData) {
		if (resData != 0) {
			switch (this.origen) {
				case 'linea': {
					this.forma.controls.codLinea.setValue(resData.jli_scodlinea);
					this.forma.controls.descLinea.setValue(resData.jli_sdeslinea);
					break;
				}
				case 'categoria': {
					this.forma.controls.codCategoria.setValue(resData.jca_scodcategoria);
					this.forma.controls.descCategoria.setValue(resData.jca_sdescategoria);
					break;
				}
				case 'agrupacion': {
					this.forma.controls.codAgrupacion.setValue(resData.jag_scodagrupacion);
					this.forma.controls.descAgrupacion.setValue(resData.jag_sdesagrupacion);
					break;
				}
				case 'prodbase': {
					this.forma.controls.codProdBase.setValue(resData.jpb_scodproductobase);
					this.forma.controls.descProdBase.setValue(resData.jpb_sdesproductobase);
					break;
				}
			}
		}
		$('body').removeClass('modal-open');
	}

    actualizarFecha() { 
		if (this.forma.controls.fecha.value === '') {
			this.forma.controls.fecha.setValue(this.utilService.dateToStringFormat(this.fechaActual));
		}	
		if (!this.utilService.DateValidator(this.forma.controls.fecha.value)) {
			this.forma.controls.fecha.setValue(this.utilService.dateToStringFormat(this.fechaActual));
		}
	}

	validaLinea() {
		if (this.forma.controls.codLinea.value === '') {
			this.forma.controls.codLinea.setValue('');
			this.forma.controls.descLinea.setValue('');
		} else {
			this.request.scodestado = 'VIGEN';
			this.request.scodlinea = this.forma.controls.codLinea.value;
			this.cargador.show();
			this.articuloService.getLineas(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.articulo;
							if (this.datos.length > 0) {
								this.forma.controls.descLinea.setValue(this.datos[0].jli_sdeslinea);
							} else {
								this.forma.controls.codLinea.setValue('');
								this.forma.controls.descLinea.setValue('');
								swal('Atención', 'No Existe Línea', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}

	validaCategoria() {
		if (this.forma.controls.codCategoria.value === '') {
			this.forma.controls.codCategoria.setValue('');
			this.forma.controls.descCategoria.setValue('');
		} else {
			this.request.scodestado = 'VIGEN';
			this.request.scodcategoria = this.forma.controls.codCategoria.value;
			this.cargador.show();
			this.articuloService.getCategorias(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.articulo;
							if (this.datos.length > 0) {
								this.forma.controls.descCategoria.setValue(this.datos[0].jca_sdescategoria);
							} else {
								this.forma.controls.codCategoria.setValue('');
								this.forma.controls.descCategoria.setValue('');
								swal('Atención', 'No Existe Categoría', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}

	validaAgrupacion() {
		if (this.forma.controls.codAgrupacion.value === '') {
			this.forma.controls.codAgrupacion.setValue('');
			this.forma.controls.descAgrupacion.setValue('');
		} else {
			this.request.scodestado = 'VIGEN';
			this.request.scodagrupacion = this.forma.controls.codAgrupacion.value;
			this.cargador.show();
			this.articuloService.getAgrupacion(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.articulo;
							if (this.datos.length > 0) {
								this.forma.controls.descAgrupacion.setValue(this.datos[0].jag_sdesagrupacion);
							} else {
								this.forma.controls.codAgrupacion.setValue('');
								this.forma.controls.descAgrupacion.setValue('');
								swal('Atención', 'No Existe Agrupación', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}

	validaProdBase() {
		if (this.forma.controls.codProdBase.value === '') {
			this.forma.controls.codProdBase.setValue('');
			this.forma.controls.descProdBase.setValue('');
		} else {
			this.request.scodestado = 'VIGEN';
			this.request.scodproductobase = this.forma.controls.codProdBase.value;
			this.cargador.show();
			this.articuloService.getProdBase(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.articulo;
							if (this.datos.length > 0) {
								this.forma.controls.descProdBase.setValue(this.datos[0].jpb_sdesproductobase);
							} else {
								this.forma.controls.codProdBase.setValue('');
								this.forma.controls.descProdBase.setValue('');
								swal('Atención', 'No Existe Producto Base', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}

	openModalLineas() {
		this.origen = 'linea';
		const initialState = {
			titulo: 'Líneas',
			origen: 'linea'
		};
		this.bsModalRef = this.modalService.show(BuscarArticuloPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	openModalCategorias() {
		this.origen = 'categoria';
		const initialState = {
			titulo: 'Categorías',
			origen: 'categoria'
		};
		this.bsModalRef = this.modalService.show(BuscarArticuloPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	openModalAgrupacion() {
		this.origen = 'agrupacion';
		const initialState = {
			titulo: 'Agrupaciones',
			origen: 'agrupacion'
		};
		this.bsModalRef = this.modalService.show(BuscarArticuloPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	openModalProdBase() {
		this.origen = 'prodbase';
		const initialState = {
			titulo: 'Productos Base',
			origen: 'prodbase'
		};
		this.bsModalRef = this.modalService.show(BuscarArticuloPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}
  setGenerar() {
	console.log("Construyo excel");
	this.cargador.show();

	this.request.nrutoperador = Number(this.user.nrutoperador);
	this.request.scodagrupacion = this.forma.controls.codAgrupacion.value;
	this.request.scodcategoria = this.forma.controls.codCategoria.value;
	this.request.scodlinea = this.forma.controls.codLinea.value;
	this.request.scodproductobase =  this.forma.controls.codProdBase.value;
	this.request.sfecha =  (this.forma.controls.fecha.value).replace(/-/g, '');

	this.reportesService.getRptItemsCantidadCero(this.request)
		.subscribe((data: any) => {
			this.request = {};
			this.cargador.hide();
			if (data.rptItemsCantidadCero.length === 0 || data.error.codError ===  500) {
				swal('Atención', 'No se encontraron resultados', 'info');
				} else {
					const auxData = data.rptItemsCantidadCero;
				/*       "art_sdescripcion": "string",
					"dpl_ncantfracciones": 0,
					"dpl_scodformato": "string",
					"dpl_scodvariedad": "string",
					"jag_scodagrupacion": "string",
					"jag_sdesagrupacion": "string",
					"jca_scodcategoria": "string",
					"jca_sdescategoria": "string",
					"jli_scodlinea": "string",
					"jli_sdeslinea": "string",
					"jpb_scodproductobase": "string",
					"jpb_sdesproductobase": "string",
					"ped_dfhocreacion": "string",
					"ped_nidpedido": 0 */
					

					const fechaExcel = this.utilService.dateToLocalStringFormat(this.fechaActual);
					const nombreExcel = 'Resporte Items con Cantidad Cero' + this.utilService.dateToStringDateFullFormat(this.fechaActual);
					//this.excelService.generateExcel(nombreExcel, headerExcel, dataExcel, fechaExcel);

					swal('Perfecto', 'Su reporte ha sido generado con exito!', 'success');

				}
		}),
		error => {
			this.cargador.hide();
			swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
			console.log('error en el request');
		};
  }
}
